// В этом файле должны быть стили для БЭМ-блока news-list, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority


/*! Список превью*/
.thumbs-list {

  $block-name:                &; // #{$block-name}__element

  .page-main--media {

    #{$block-name}__inner {
      grid-template-rows: 276px;
    }
  }

  &__inner {
    display: grid;
    // grid-template-rows: auto auto auto;
    grid-template-columns: 1fr;
    grid-gap: 16px;
    margin: 0;
    padding: 0;
    list-style: none;

    @media (min-width: $screen-md) {
      grid-template-columns: 1fr 1fr;
    }

    @media (min-width: $screen-lg) {
      grid-template-columns: 1fr 1fr 1fr;
    }

    @media (min-width: $screen-xl) {
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }
  }

  .thumb-card {
    margin-left: -16px;
    margin-right: -16px;

    @media (min-width: $screen-md) {
      margin-left: 0;
      margin-right: 0;
    }
  }


  &--line {

    .thumb-card {

      &:nth-child(n+4) {

        @media (min-width: $screen-lg) {
          display: none;
        }

        @media (min-width: $screen-xl) {
          display: -webkit-flex;
          display: -moz-flex;
          display: -ms-flex;
          display: -o-flex;
          display: flex;
        }
      }
    }
  }
}


@mixin card-01 {
  background-color: #fff;
  color: $black;

  .thumb-card__date {
    color: rgba(0, 0, 0, 0.64);
  }

  .thumb-card__text {
    display: block;
    color: $black;
  }

  .thumb-card__img {
    display: none;
  }

  .thumb-card__title {
    color: $black;
  }
}

@mixin card-02 {
  color: $white;

  .thumb-card__text {
    display: none;
    color: $white;
  }

  .thumb-card__date {
    color: rgba(255, 255, 255, 0.64);
  }

  .thumb-card__title {
    color: $white;
  }

  .thumb-card__img {
    display: block;
  }
}

.thumbs-list--news .thumb-card:nth-child(2n-1) {
  @include card-01;
}

.thumbs-list--news .thumb-card:nth-child(2n) {
  @include card-02;
}

@media (min-width: $screen-md) {
  .thumbs-list--news .thumb-card:nth-child(-n+2):nth-child(2n):nth-child(n),
  .thumbs-list--news .thumb-card:nth-child(n+3):nth-child(-n+4):nth-child(2n-1),
  .thumbs-list--news .thumb-card:nth-child(n+5):nth-child(-n+6):nth-child(2n),
  .thumbs-list--news .thumb-card:nth-child(n+7):nth-child(-n+8):nth-child(2n-1),
  .thumbs-list--news .thumb-card:nth-child(n+9):nth-child(-n+10):nth-child(2n),
  .thumbs-list--news .thumb-card:nth-child(n+11):nth-child(-n+12):nth-child(2n-1),
  .thumbs-list--news .thumb-card:nth-child(n+13):nth-child(-n+14):nth-child(2n)
  {
    @include card-01;
  }

  .thumbs-list--news .thumb-card:nth-child(-n+2):nth-child(2n-1):nth-child(n),
  .thumbs-list--news .thumb-card:nth-child(n+3):nth-child(-n+4):nth-child(2n),
  .thumbs-list--news .thumb-card:nth-child(n+5):nth-child(-n+6):nth-child(2n-1),
  .thumbs-list--news .thumb-card:nth-child(n+7):nth-child(-n+8):nth-child(2n),
  .thumbs-list--news .thumb-card:nth-child(n+9):nth-child(-n+10):nth-child(2n-1),
  .thumbs-list--news .thumb-card:nth-child(n+11):nth-child(-n+12):nth-child(2n),
  .thumbs-list--news .thumb-card:nth-child(n+13):nth-child(-n+14):nth-child(2n-1)
  {
    @include card-02;
  }
 }

@media (min-width: $screen-lg) {

  .thumbs-list--news .thumb-card:nth-child(-n+3):nth-child(2n):nth-child(n),
  .thumbs-list--news .thumb-card:nth-child(n+4):nth-child(-n+6):nth-child(2n),
  .thumbs-list--news .thumb-card:nth-child(n+7):nth-child(-n+9):nth-child(2n),
  .thumbs-list--news .thumb-card:nth-child(n+10):nth-child(-n+12):nth-child(2n)
  {
    @include card-01;
  }

  .thumbs-list--news .thumb-card:nth-child(-n+3):nth-child(2n-1):nth-child(n),
  .thumbs-list--news .thumb-card:nth-child(n+4):nth-child(-n+6):nth-child(2n-1),
  .thumbs-list--news .thumb-card:nth-child(n+7):nth-child(-n+9):nth-child(2n-1),
  .thumbs-list--news .thumb-card:nth-child(n+10):nth-child(-n+12):nth-child(2n-1)
  {
    @include card-02;
  }
}

@media (min-width: $screen-xl) {

  .thumbs-list--news .thumb-card:nth-child(-n+4):nth-child(2n):nth-child(n),
  .thumbs-list--news .thumb-card:nth-child(n+5):nth-child(-n+8):nth-child(2n-1),
  .thumbs-list--news .thumb-card:nth-child(n+9):nth-child(-n+12):nth-child(2n)
  {
    @include card-01;
  }

  .thumbs-list--news .thumb-card:nth-child(-n+4):nth-child(2n-1):nth-child(n),
  .thumbs-list--news .thumb-card:nth-child(n+5):nth-child(-n+8):nth-child(2n),
  .thumbs-list--news .thumb-card:nth-child(n+9):nth-child(-n+12):nth-child(2n-1)
  {
    @include card-02;
  }
}
