// В этом файле должны быть стили для БЭМ-блока phone, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.phone {

  $block-name:                &; // #{$block-name}__element

  display: flex;
  align-items: center;
  font-family: $font-family--header;
  color: #ffffff;
  text-decoration: none;

  &:hover,
  &:active,
  &:focus {
    color: #ffffff;
  }

  &::before {
    @include sprite($phone);
    content: '';
    display: block;
    margin-right: 14px;
  }

  &__number {
    font-size: 18px;
    font-weight: 700;
    color: inherit;
    text-decoration: none;
  }

  &__note {
    font-size: 11px;
  }

  &--header {
    display: none;

    @media (min-width: $screen-md) {
      display: flex;
      margin-right: 40px;
      margin-bottom: 10px;
      margin-left: auto;
    }

    @media (min-width: $screen-lg) {
      margin-right: 100px;
    }

    @media (min-width: $screen-xl) {
      margin-right: 70px;
    }

    @media (min-width: $screen-xxl) {
      margin-right: 142px;
    }
  }

  &--footer {
    margin-bottom: 22px;
    grid-column-start: 1;
    grid-column-end: 3;

    @media (min-width: $screen-md) {
      grid-row-start: 3;
      grid-column-start: 1;
      grid-column-end: 2;
      width: 250px;
      margin-bottom: 2px;
    }

    @media (min-width: $screen-lg) {
      align-self: end;
      width: auto;
      margin-right: 55px;
    }

    @media (min-width: $screen-xl) {
      grid-row-start: 2;
      grid-column-end: 2;
      margin-right: 65px;
    }
  }
}
