// В этом файле должны быть стили для БЭМ-блока inner-nav, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

/*! Внутреннее меню страницы */
.inner-nav {

  $block-name:                &; // #{$block-name}__element

 &__list {
    margin: 0;
    padding: 0;
    list-style: none;

    @media (min-width: $screen-lg) {
      padding-top: 0;
    }
  }

  &__item {

    &--active,
    &.multi-tabs__link-wrap--active {
      // display: none;

      #{$block-name}__link {
        color: $red;
      }

      // @media (min-width: $about-menu-desktop-width) {
      //   display: block;
      // }
    }

    &:last-child {
      #{$block-name}__link {
        padding-bottom: 0;
      }
    }

    &.tabs__link-wrap {
      margin-bottom: 0;
    }
  }

  &__link {
    display: block;
    padding-top: 0.9em;
    padding-bottom: 0.9em;
    font-size: 16px;
    font-weight: 700;
    color: #000000;
    text-decoration: none;

    &:hover,
    &:focus,
    &:active {
      color: $red;
      outline: none;
    }
  }
}
