// В этом файле должны быть стили для БЭМ-блока reviews-slider, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.reviews-slider {

  $block-name:                &; // #{$block-name}__element


  font-family: $font-family--header;
  color: #000;
  display: flex;
  padding-bottom: 0;

  &__wrapper {
    position: relative;

    .tns-outer {
      height: 100%;
    }

    .tns-ovh {
      height: 100%;
    }

    .tns-inner {
      height: 100%;
    }

    .tns-slider {
      height: 100%;
    }

    .tns-controls {
      position: absolute;
      right: $grid-gutter-width;
      top: $grid-gutter-width;
      z-index: 10;
    }
  }

  &__item {
    display: flex;
    padding-bottom: 0;
    background-color: $section-bg--light;

    &.page-card {

      @media (min-width: $screen-md) {
        margin-bottom: 0;
      }
    }
  }

  &__item-inner {
    display: grid;
    grid-template-rows: 1fr auto auto;
    grid-template-columns: 1fr auto;
    height: 100%;
  }

  &__cite {
    position: relative;
    margin-bottom: 40px;
    padding-top: 64px;
    grid-row-start: 1;
    grid-row-end: 2;
    grid-column-start: 1;
    grid-column-end: 3;
    font-size: 18px;
    font-weight: 700;

    @media (min-width: $screen-md) {
      margin-bottom: 70px;
    }

    @media (min-width: $screen-lg) {
      margin-bottom: 0;
    }

    &::before {
      @include sprite($quotes-red);
      content: '';
      position: absolute;
      top: 0;
      left: 0;
    }
  }

  &__name {
    grid-row-start: 2;
    grid-row-end: 3;
    grid-column-start: 1;
    grid-column-end: 2;
    align-self: center;
    margin-top: 5px;
    margin-bottom: 10px;
    font-size: 16px;
    font-weight: 700;
  }

  &__desc {
    grid-row-start: 3;
    grid-row-end: 4;
    grid-column-start: 1;
    grid-column-end: 2;
    margin-bottom: 20px;
    font-size: 13px;

    @media (min-width: $screen-md) {
      margin-right: 30px;
    }
  }

  &__img {
    grid-row-start: 2;
    grid-row-end: 4;
    align-self: end;
    font-size: 0;
  }

  .page-main--index & {

    &__wrapper {

      @media (min-width: $screen-md) {
        grid-row-start: 2;
        grid-row-end: 3;
        margin-bottom: 0;
      }

      @media (min-width: $screen-lg) {
        grid-row-start: 1;
        grid-row-end: 2;
        grid-column-start: 3;
        grid-column-end: 4;
      }

      @media (min-width: $screen-xl) {
        grid-column-start: 4;
        grid-column-end: 5;
      }
    }
  }
}
