// В этом файле должны быть стили для БЭМ-блока person, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.person {

  $block-name:                &; // #{$block-name}__element
  align-self: start;
  margin-bottom: $grid-gutter-width;
  border-radius: $border-radius;
  overflow: hidden;

  @media (min-width: $screen-lg) {
    margin-bottom: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }

  &__inner {
    display: flex;
    flex-direction: column;
    min-height: 330px;
    padding-top: 16px;
    padding-bottom: 24px;
    padding-right: 16px;
    padding-left: 16px;
    font-family: $font-family--header;
    line-height: 1.2;
    color: #000;
    background-color: #fff;
  }

  &__header {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }

  &__img {
    flex-shrink: 0;
    width: 80px;
    height: 80px;
    margin-right: 14px;
    background-color: rgba(14, 9, 9, 0.16);
    border-radius: 50%;
    overflow: hidden;

    img {
      width: 99%;
      height: 99%;
      object-fit: contain;
    }
  }

  &__caption {
    flex-grow: 1;
  }

  &__name {
    font-size: 16px;
    font-weight: 700;
  }

  &__position {
    align-self: start;
    margin-top: 10px;
    font-size: 10px;
    text-transform: uppercase;
  }

  &__level {
    width: 240px;
    min-height: (14px * 2);
    margin-top: auto;
    margin-bottom: auto;
    font-size: 11px;
    line-height: 14px;
    color: rgba(0, 0, 0, 0.43);
    text-transform: uppercase;
  }

  &__data {
    margin-top: auto;
    margin-bottom: auto;
  }

  &__data-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #dcdcdc;

    &:first-child {
      padding-top: 0;
    }

    &:last-child {
      padding-bottom: 0;
      border-bottom: none;
    }
  }

  &__data-name {
    font-size: 11px;
    line-height: 16px;
    color: #adadad;
    text-transform: uppercase;
  }

  &__data-val {
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    text-transform: uppercase;
  }

  &__title-list {
    width: 240px;
    margin-top: auto;
    margin-bottom: auto;
    padding: 0;
    list-style: none;
    font-size: 13px;
  }

  &__item {
    padding-top: 8px;
    padding-bottom: 8px;
    border-bottom: 1px solid #dcdcdc;

    &:first-child {
      padding-top: 0;
    }

    &:last-child {
      padding-bottom: 0;
      border-bottom: none;
    }
  }

  &__title-item {
    display: flex;
    align-items: center;
    padding-top: 6px;
    padding-bottom: 8px;
    font-size: 13px;
    line-height: 16px;
    color: inherit;
    border-bottom: 1px solid #dcdcdc;

    &:first-child {
      padding-top: 0;
    }

    &:last-child {
      border-bottom: none;
      padding-bottom: 0;
    }
  }

  &__more {
    align-self: flex-start;
    margin-top: auto;
    padding-left: 0;
    padding-bottom: 0;
  }

  &--flag {
    position: relative;
    width: 290px;
    padding-bottom: 45px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 0;
      height: 0;
      border-bottom: 45px solid transparent;
      border-left: 145px solid #fff;
      border-right: 145px solid #fff;
      border-bottom-left-radius: 8px;
      border-bottom-right-radius: 8px;
    }

    #{$block-name}__inner {
      min-height: 285px;
    }

    #{$block-name}__position {
      opacity: 0.72;
    }
  }

  &--red {

    #{$block-name}__inner {
      background-color: $red;
      color: #fff;
    }

    #{$block-name}__more {
      color: #000;

      &:hover,
      &:focus,
      &:active {
        color: #595959;
      }
    }

    &::after {
      border-left-color: $red;
      border-right-color: $red;
    }
  }

  &--black {

    #{$block-name}__inner {
      background-color: #000;
      color: #fff;
    }

    #{$block-name}__img {
      background-color: rgba(255, 255, 255, 0.16);
    }

    &::after {
      border-left-color: #000;
      border-right-color: #000;
    }
  }

  &--address {
    margin-bottom: 15px;
    border: 1px solid rgba(0, 0, 0, 0.15);
    border-radius: $border-radius;

    #{$block-name}__inner {
      min-height: 116px;
    }

    #{$block-name}__img {
      width: 64px;
      height: 64px;
    }

    #{$block-name}__header {
      margin-bottom: 0;
    }
  }
}
