// В этом файле должны быть стили для БЭМ-блока note, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.note {

  $block-name:                &; // #{$block-name}__element

  padding-top: 20px;
  background-image: url('../img/bg-circle-v.png');
  background-repeat: no-repeat;
  background-position: center 20px;

  @media (min-width: $screen-md) {
    min-height: 200px;
    padding-top: 30px;
    background-image: url('../img/bg-circle-h.png');
    background-position: 90px 0;
  }

  @media (min-width: $screen-lg) {
    background-position: 150px 0;
  }

  @media (min-width: $screen-xxl) {
    background-position: 430px 0;
  }

  &.page-card {
    background-color: transparent;
  }

  &__title {
    margin-bottom: 20px;
    font-family: $font-family;
    font-size: 30px;
    font-weight: 700;
  }

  &__lead {
    margin-bottom: 26px;
    font-family: $font-family--header;
    font-size: 18px;
    font-weight: 700;
    color: $red;

    @media (min-width: $screen-md) {
      padding-left: 25px;
    }
  }

  &__text {
    font-family: $font-family;
    font-size: 13px;
    line-height: 1.38;

    @media (min-width: $screen-md) {
      margin-top: 25px;
    }


    p {
      margin: 0;
    }
  }

  .page-main--index & {

    @media (min-width: $screen-md) {
      grid-column-start: 1;
      grid-column-end: 3;
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 0;
    }

    @media (min-width: $screen-lg) {
      grid-column-end: 4;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: auto;
      gap: $grid-gutter-width;
      padding-top: 40px;
      padding-bottom: 30px;
      padding-left: 0;
      padding-right: 0;
    }

    @media (min-width: $screen-xl) {
      grid-column-end: 5;
      grid-template-columns: repeat(4, 1fr);
      padding-top: 30px;
      padding-bottom: 20px;
    }

    &__title {

      @media (min-width: $screen-md) {
        flex-basis: 50%;
      }

      @media (min-width: $screen-lg) {
        padding-left: $grid-gutter-width;
      }
    }

    &__lead {

      @media (min-width: $screen-md) {
        flex-basis: 50%;
        align-self: flex-end;
      }

      @media (min-width: $screen-lg) {
        margin-top: 10px;
        align-self: start;
      }
    }

    &__text {

      @media (min-width: $screen-md) {
        flex-basis: 100%;
      }

      @media (min-width: $screen-lg) {
        margin-top: 10px;
        padding-right: $grid-gutter-width;
        padding-left: $grid-gutter-width;
      }

      @media (min-width: $screen-xl) {
        grid-column-start: 3;
        grid-column-end: 5;
      }
    }
  }
}
