  margin-left: 0;
  margin-right: 0;
}

// samp,
code,
kbd,
pre {
  font-family: $font-family--monospace;
}

code {
  padding: 0.06em 0.3em;
  color: $text-color;
  background-color: rgba(#000, 0.08);
  border-radius: $border-radius;
}

kbd {
  padding: 0.06em 0.3em;
  color: $text-color;
  background-color: $gray-lightest;
  border-radius: $border-radius;

  kbd {
    padding: 0;
  }
}

pre {
  display: block;
  width: 100%;
  margin-bottom: $typo-margin-vertical;
  overflow-x: auto;
  tab-size: 2;
  background-color: rgba(#000, 0.08);

  code {
    background: transparent;
  }
}

/*! Вспомогательный элемент для установки ширины полей форм по содрежимому */
.buffer {
  position: absolute;
  z-index: -1;
  visibility: hidden;
  white-space: nowrap;
}


.page-main {
  @include container;

  &--news-list,
  &--media {

    .page-title {
      margin-bottom: 30px;

      @media (min-width: $screen-md) {
        margin-bottom: 48px;
      }

      @media (min-width: $screen-lg) {
        margin-bottom: 60px;
      }
    }
  }

  &--article {

    .article + .page-card--thumbs-line {
      margin-bottom: 0;
      padding-left: 0;
      padding-right: 0;
      background-color: transparent;

      .thumb-card {
        margin-right: 0;
        margin-left: 0;
      }
    }
  }
}


.page-card {
  margin-bottom: 16px;
  padding-top: 18px;
  padding-bottom: 16px;
  padding-right: 16px;
  padding-left: 16px;
  border-radius: $border-radius;
  background-color: $section-bg;
  overflow: hidden;

  @media (min-width: $screen-md) {
    margin-bottom: 24px;
  }

  &--thumbs-line {
    padding-bottom: 24px;
    background-color: transparent;

    @media (min-width: $screen-md) {
      background-color: #090808;
    }
  }

  &--news-line {
    background-color: #252525;
  }
}

.title-rich {
  position: relative;
  display: flex;
  padding-top: 0.6em;

  &:hover,
  &:focus,
  &:active {
    color: #fff;
  }

  & > * {
    position: relative;
    padding-left: 0.5em;
  }

  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 2.5em;
    height: 2.5em;
    background-color: $red;
    // transform: translateY(-50%);
  }
}

