// В этом файле должны быть стили для БЭМ-блока account-btn, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.user {

  $block-name:                &; // #{$block-name}__element

  &__enter {
    display: flex;
    align-self: flex-start;
    color: #fff;
    text-decoration: none;
    cursor: pointer;

    &:hover,
    &:focus,
    &:active {
      color: $red;
    }

    span {
      font-size: 0;
      color: inherit;

      @media (min-width: $screen-xxl) {
        display: inline-block;
        margin-right: 16px;
        font-size: 13px;
        font-weight: 700;
        letter-spacing: 2.6px;
        text-align: right;
        font-family: $font-family--header;
        white-space: pre-line;
      }
    }

    &::after {
      content: '';
      display: block;
      width: 22px;
      height: 30px;
      background-image: url('../img/account-red.png');
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
    }
  }

  &__greeting {
    display: none;

    @media (min-width: $screen-md) {
      display: inline-block;
      font-size: 12px;
      line-height: 16px;
      font-weight: 300;
      letter-spacing: 0.6px;
      text-align: right;
      color: rgba(255, 255, 255, 0.4);
    }

    @media (min-width: $screen-xl) {
      margin-right: 10px;
    }
  }

  &__name {
    font-family: $font-family--header;
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    text-align: right;
    color: $red;
    word-break: keep-all;
    cursor: pointer;

    @media (min-width: $screen-md) {
      font-size: 14px;
      letter-spacing: 2.8px;
    }
  }

  &__exit {
    padding-top: 3px;
    padding-right: 0;
    font-size: 11px;
    font-weight: 700;
    letter-spacing: 0.55px;
    color: #000;
    text-align: right;
    justify-self: end;

    @media (min-width: $screen-xl) {
      grid-column-start: 2;
      grid-column-end: 3;
    }
  }

  &__pic {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 4;
    align-self: start;
    width: 32px;
    height: 32px;
    margin-left: 8px;
    border-radius: 50%;
    overflow: hidden;

    @media (min-width: $screen-md) {
      width: 44px;
      height: 44px;
    }

    @media (min-width: $screen-xl) {
      grid-column-start: 3;
      grid-column-end: 4;
      grid-row-start: 1;
      grid-row-end: 3;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: scale-down;
      font-family: 'object-fit: scale-down;';
    }
  }

  &__logged {
    display: none;
  }

  &--header {
    margin-bottom: 13px;
  }

  &--logged {
    margin-bottom: 0;


    #{$block-name}__enter {
      display: none;
    }

    #{$block-name}__logged {
      display: grid;
      grid-template-columns: auto auto;
      grid-template-rows: auto auto auto;

      & > * {
        align-itmes: end;
      }

      @media (min-width: $screen-xl) {
        grid-template-columns: auto auto auto;
        grid-template-rows: auto auto;
      }
    }

  }

}
