// &__pagination .swiper-pagination-bullet,
.tns-nav button {
  @include btn;
  width: 7px;
  height: 7px;
  padding: 0;
  margin-right: 8px;
  background-color: #fff;
  opacity: 1;
}

.tns-nav .tns-nav-active {
  background-color: #fc0202;
}

