// В этом файле должны быть стили для БЭМ-блока title, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority


/* !Заголовок */

.title {

  $block-name:                &; // #{$block-name}__element

  position: relative;
  display: flex;
  align-items: center;
  font-size: 30px;
  line-height: $line-height;
  margin-bottom: 30px;

  @media (min-width: $screen-md) {
    font-size: 36px;
    margin-bottom: 0;
  }

  & > * {
    position: relative;
    display: block;
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 16px;
    // padding-right: 8px;
    font-family: $font-family--header;
    font-size: inherit;
    font-weight: 700;
    color: currentColor;
    white-space: pre-line;

    @media (min-width: $screen-md) {
      // padding-left: 18px;
      // padding-right: 0;
    }
  }

  & > *:first-letter {
    text-transform: uppercase;
  }

  &--rich {

    &::before {
      content: '';
      position: absolute;
      width: 48px;
      height: 48px;
      background-color: $red;
      left: 0;
      top: 0.5em;
      transform: translateY(-50%);

      @media (min-width: $screen-md) {
        width: 64px;
        height: 64px;
        top: 0.6em;
      }
    }
  }
}
