// В этом файле должны быть стили для БЭМ-блока news-card, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

/*! Карточка новости или медиа */
.thumb-card {

  $block-name:                &; // #{$block-name}__element
  $columns-in-row:            4;

  position: relative;
  display: flex;
  height: 276px;
  border-radius: $border-radius;
  line-height: $line-height;
  overflow: hidden;
  z-index: 1;

  a#{$block-name}__inner {

    &:hover,
    &:active,
    &:focus {
      color: currentColor;
      outline: none;
      #{$block-name}__more {
        color: $link-red-hover;
      }
    }
  }

  &__inner {
    display: flex;
    // padding: 20px 15px;
    text-decoration: none;
    color: currentColor;
  }

  &__content {
    display: flex;
    flex-direction: column;
  }

  &__date {
    margin-bottom: 10px;
    font-family: $font-family--header;
    font-size: 10px;
    font-weight: 400;
    letter-spacing: 2px;
    text-transform: uppercase;
  }

  &__title-add {
    font-family: $font-family--header;
    font-size: 11px;
    font-weight: 400;
    letter-spacing: 2.2px;
    text-transform: uppercase;
  }

  &__title {
    margin-bottom: 10px;
    font-family: $font-family--header;
    font-size: 16px;
    font-weight: 700;
    color: currentColor;
  }

  &__text {
    flex-grow: 1;
    font-family: $font-family;
    font-size: 12px;
    font-weight: 400;
    color: $black;
  }

  &__more {
    margin-top: auto;
    font-family: $font-family--header;
    font-size: 11px;
    font-weight: 700;
    line-height: 1.6;
    letter-spacing: 0.55px;
    color: $red;
    text-transform: uppercase;
    text-decoration: none;
    cursor: pointer;

    &:hover,
    &:active {
      text-decoration: none;
      color: $link-red-hover;
      outline: none;
    }
  }

  &__img {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    z-index: -1;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__video {
    width: 100%;
    height: 100%;
    outline: 1px solid red;
    display: none;
  }

  &--video {

    #{$block-name}__title {
      position: relative;
      padding-top: 50px;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 42px;
        height: 38px;
        background-color: $red;
      }

      &::after {
        content: '';
        position: absolute;
        top: 9px;
        left: 14px;
        width: 0;
        height: 0;
        border-width: 10px 0 10px 16px;
        border-color: transparent transparent transparent #100f0f;
        border-style: solid;
      }
    }

    #{$block-name}__inner {
      cursor: pointer;
    }
  }

  &--media {
    background-color: #090808;

    &.page-card {
      margin-bottom: 0;
    }

    #{$block-name}__title-add {
      margin-bottom: 14px;
      font-size: 10px;
      color: rgba(255, 255, 255, 0.64);
    }

    #{$block-name}__title {
      margin-top: 0;
      margin-bottom: 0;
      width: 65%;
    }

    #{$block-name}__content {
      margin-top: auto;
    }

    #{$block-name}__inner {
      cursor: pointer;
    }
  }

  &--small {
    height: 154px;

  }

  .page-main--index & {

    @media (min-width: $screen-md) {
      margin-bottom: 0;
    }

    &__title-add {
      margin-top: 12px;
      font-size: 11px;
      font-weight: 400;
      letter-spacing: 2.2px;
    }

    &__title {
      margin-top: auto;
    }

    &__content {
      margin-top: 0;
    }
  }
}
