// В этом файле должны быть стили для БЭМ-блока results, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.results-slider {

  $block-name:                &; // #{$block-name}__element

  font-family: $font-family--header;
  border-radius: $border-radius;
  background-color: $section-bg;
  overflow: hidden;

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px 16px;
    background-color: #dcdcdc;
  }

  &__title-add {
    margin-bottom: 8px;
    font-size: 11px;
    letter-spacing: 2.2px;
    color: rgba(0, 0, 0, 0.4);
    text-transform: uppercase;
  }

  &__title {
    font-size: 18px;
    font-weight: 900;
    color: #000;
    text-transform: uppercase;
  }

  &__date {
    font-size: 14px;
    font-weight: 700;
    text-align: right;
    color: #fff;
  }

  &__day {
    font-size: 36px;
    line-height: 1;
  }

  .page-main--index & {

    @media (min-width: $screen-md) {
      grid-row-start: 2;
      grid-row-end: 3;
      margin-bottom: 0;
    }

    @media (min-width: $screen-lg) {
      grid-row-start: 1;
      grid-row-end: 2;
      grid-column-start: 3;
      grid-column-end: 4;
    }

    @media (min-width: $screen-xl) {
      grid-column-start: 4;
      grid-column-end: 5;
    }
  }

  &__wrapper {

    .tns-controls {
      position: absolute;
      right: $grid-gutter-width;
      bottom: 24px;
    }
  }
}
