// В этом файле должны быть стили для БЭМ-блока person-large, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.person-large {

  $block-name:                &; // #{$block-name}__element

  position: relative;
  padding: 24px 16px 0;
  color: #adadad;
  background-color: #fff;
  border-radius: $border-radius;
  text-decoration: none;
  overflow: hidden;

  @media (min-width: $screen-lg) {
    height: 330px;
  }

  @media (min-width: $screen-xl) {
    display: grid;
    grid-template-rows: auto 150px 1fr auto;
    grid-template-columns: 1fr 1fr;
  }


  &__position {
    grid-row-start: 1;
    grid-row-end: 2;
    grid-column-start: 1;
    grid-column-end: 3;
    margin-bottom: 10px;
    font-family: $font-family--header;
    font-size: 11px;
    letter-spacing: 2.2px;
    color: #fc0202;
    text-transform: uppercase;

    @media (min-width: $screen-lg) {
      margin-bottom: 0;
    }
  }

  &__name {
    grid-row-start: 2;
    grid-row-end: 3;
    grid-column-start: 1;
    grid-column-end: 2;
    align-self: end;
    margin-bottom: 10px;
    font-family: $font-family--header;
    font-size: 24px;
    font-weight: 700;
    line-height: 28px;
    color: #000000;

    @media (min-width: $screen-md) {
      min-height: 56px;
    }
  }

  &__level {
    grid-row-start: 3;
    grid-row-end: 4;
    grid-column-start: 1;
    grid-column-end: 2;
    align-self: start;
    margin-bottom: 10px;
    font-family: $font-family--header;
    font-size: 11px;
    line-height: 15px;
    color: #adadad;
    text-transform: uppercase;

    @media (min-width: $screen-md) {
      min-height: 30px;
    }
  }

  &__more {
    grid-row-start: 4;
    grid-row-end: 5;
    grid-column-start: 1;
    grid-column-end: 2;
    justify-self: start;
    padding-left: 0;
    padding-bottom: 16px;
    padding-top: 10px;

    @media (min-width: $screen-lg) {
      position: absolute;
      bottom: 0;
      left: $grid-gutter-width;
    }

    @media (min-width: $screen-xl) {
      position: static;
      padding-bottom: 24px;
      padding-top: 0;
    }
  }

  &__img {
    grid-row-start: 2;
    grid-row-end: 5;
    grid-column-start: 2;
    grid-column-end: 3;
    height: 220px;

    @media (min-width: $screen-lg) {
      height: 150px;
    }

    @media (min-width: $screen-xl) {
      height: auto;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: top;
    }
  }
}
