// В этом файле должны быть стили для БЭМ-блока product-card, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.product-card {

  $block-name:                &; // #{$block-name}__element
  position: relative;
  display: flex;
  min-height: 280px;
  font-family: $font-family--header;
  color: #000;
  background-color: #dcdcdc;
  border-radius: 6px;
  overflow: hidden;

  @media (min-width: $screen-md) {
    min-height: 330px;
  }

  &__inner {
    position: relative;
    display: flex;
    flex-direction: column;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 15px;
    padding-right: 15px;
    z-index: 10;
  }

  &__title {
    width: 50%;
    margin-bottom: 15px;
    font-size: 18px;
    line-height: 1.2;
    font-weight: 700;
    word-break: keep-all;

    @media (min-width: $screen-lg) {
      width: auto;
      margin-bottom: 25px;
      white-space: pre-line;
    }
  }

  &__text {
    width: 195px;
    margin-bottom: 15px;
    font-size: 11px;
    line-height: 1.2;
    color: #adadad;
    text-transform: uppercase;
    white-space: pre-line;
  }

  &__bottom {
    display: flex;
    align-items: center;
    margin-top: auto;
  }

  &__price {
    font-size: 18px;
    font-weight: 700;

    .curr {
      margin-left: 5px;
      font-weight: 600;
    }
  }

  .product-slider__img {

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__cart {
    margin-right: 28px;
  }

  @media (min-width: $screen-lg) {
    width: 443px;
  }

}

.product-slider__wrapper.slider__wrapper {
  position: absolute;
  top: 0;
  bottom: 0;

  .tns-controls {
    position: absolute;
    bottom: 16px;
    right: 16px;
    z-index: 10;

    button {
      background-color: #fff;
    }
  }
}
