// В этом файле должны быть стили для БЭМ-блока field-text, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.field-text {

  $block-name:                &; // #{$block-name}__element

  display: block;

  &__name {
    @include field-name;
  }

  &__input-wrap {
    display: block;
  }

  &__input {
    @include field-text;

    @at-root textarea#{&} {
      $textarea-height: ($line-height * 2) + ($field-padding-vertical * 2);

      height: auto;
      min-height: calc(#{$textarea-height} + 2px);
      resize: vertical;
    }
  }

  &__help-text {
    @include field-help-text;
  }



  &--error {

    #{$block-name}__name,
    #{$block-name}__input,
    #{$block-name}__help-text {
      color: $color-danger;
    }

    #{$block-name}__input {
      border-color: $color-danger;
      background-color: lighten($color-danger, 35%);
    }
  }


  &--auth {
    margin-bottom: 20px;
    font-family: $font-family--header;
    color: #fff;

    @media (min-width: $screen-xl) {
      margin-bottom: 5px;
    }

    #{$block-name}__name {
      font-size: 10px;
      font-weight: 600;
      letter-spacing: 2px;
      color: rgba(255, 255, 255, 0.4);
      text-transform: uppercase;
    }

    #{$block-name}__input {
      padding-left: 0;
      font-size: 12px;
      font-weight: 600;
      letter-spacing: 0.6px;
      color: #ffffff;
      border-bottom-color: #ffffff;
      text-transform: uppercase;
    }

    #{$block-name}__help-text {
      display: none;
    }


    &#{$block-name}--error {

      #{$block-name}__name,
      #{$block-name}__input {
        background-color: rgba(252, 2, 2, 0.24);
      }

      #{$block-name}__input {
        border-bottom-color: $red;
      }

      #{$block-name}__help-text {
        display: flex;
        justify-content: space-between;
        margin-top: 8px;
        font-size: 11px;
        letter-spacing: 0.55px;
        color: #fc0202;

        span {
          font-family: $font-family;
        }

        a {
          font-family: $font-family--header;
          font-weight: 700;
          text-decoration: none;
          color: inherit;

          &:hover,
          &:focus,
          &:active {
            color: inherit;
          }
        }
      }

      &#{$block-name}--register {

        #{$block-name}__name,
        #{$block-name}__input {
          background-color: rgba(252, 255, 255, 0.24);
        }

        #{$block-name}__input {
          border-bottom-color: #ffffff;
        }

        #{$block-name}__help-text {
          color: #ffffff;
        }
      }
    }
  }

  &--msg {
    width: 260px;

    #{$block-name}__input {
        padding-left: 0;
        font-size: 10px;
        font-weight: 600;
        letter-spacing: 2px;
        text-transform: uppercase;

      &::placeholder {
        font-size: 10px;
        font-weight: 600;
        letter-spacing: 2px;
        text-transform: uppercase;
        color: rgba(0, 0, 0, 0.4);
      }
    }
  }

  &--time {

    #{$block-name}__input-wrap {
      position: relative;
      cursor: pointer;

      &::after {
        @include sprite($arrow-down-black);
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
      }
    }
  }
}
