// В этом файле должны быть стили для БЭМ-блока article, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority


/*! Статья*/
.article {

  $block-name:                &; // #{$block-name}__element
  color: $black;
  padding: 0;

  &__header {
    font-family: $font-family--header;
    color: $white;

    @media (min-width: $screen-lg) {
      position: relative;
      z-index: 10;
    }
  }

  &__header-inner {
    position: relative;
    width: 100%;
    padding: 24px 16px;
    z-index: 10;

    @media (min-width: $screen-lg) {
      position: static;
      display: flex;
      justify-content: space-between;
      height: 526px;
      z-index: 0;
    }
  }

  &__title {
    // padding-left: 16px;
    // padding-right: 16px;
    color: currentColor;

    @media (min-width: $screen-lg) {
      padding: 0;
    }

    @media (min-width: $screen-md) {
      margin-top: 0;
    }

    .title {

      & > * {
        hyphens: auto;
      }

      @media (min-width: $screen-lg) {
        width: 740px;
        max-width: 90%;
      }
    }
  }

  &__subject {
    display: none;

    @media (min-width: $screen-md) {
      display: block;
      margin-top: 30px;
      font-size: 11px;
      letter-spacing: 2.2px;
      color: rgba(255, 255, 255, 0.4);
      text-transform: uppercase;
    }

    @media (min-width: $screen-lg) {
      margin-top: 0;
    }
  }

  &__slogan {
    display: none;

    @media (min-width: $screen-md) {
      display: block;
      font-size: 24px;
      font-weight: 700;
      text-align: right;
      white-space: pre-line;
      justify-self: end;
    }

    @media (min-width: $screen-xl) {
      width: 600px;
      font-size: 30px;
    }
  }

  &__excerpt {
    font-family: $font-family--header;
    font-size: 12px;
    font-weight: 700;
    line-height: 1.5;
  }

  &__header-warning {
    position: absolute;
    left: 314px;
    bottom: 50px;
  }

  &__img {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    font-size: 0;
    opacity: 0.72;
    border-radius: $border-radius;
    overflow: hidden;
    z-index: -1;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__next.thumb-card {
    color: $white;
    margin-bottom: 0;
    height: auto;
    background-color: transparent;
    box-sizing: border-box;

    @media (min-width: $screen-lg) {
      padding: 0;
    }
  }

  &__next .thumb-card {

    &__inner {
      position: relative;
      padding-right: 40px;
      padding-left: 0;

      @media (min-width: $screen-lg) {
        margin-top: auto;
      }
    }

    &__content {
      flex-basis: 100%;

      @media (min-width: $screen-lg) {
        flex-basis: auto;
      }
    }

    &__title {
      position: relative;
      margin-bottom: 0;

      &::after {
        @include sprite($arrow-large-right);
        content: '';
        position: absolute;
        top: 50%;
        right: -30px;
        transform: translateY(-50%);
      }

      &:hover {

        &::after {
          @include sprite($arrow-large-right-red);
        }
      }
    }
  }


  &__main {
    display: flex;
    flex-wrap: wrap;
    padding-top: 24px;
    padding-bottom: 24px;
    padding-left: $grid-gutter-width;
    padding-right: $grid-gutter-width;
    background-color: #fff;
  }

  &__aside {
    flex-shrink: 0;
    margin-left: auto;
    margin-right: auto;

    @media (min-width: $screen-lg) {
      // width: 288px;
      display: flex;
      flex-direction: column;
      margin-right: 0;
      margin-left: 50px;
    }

    .social {
      align-self: flex-start;
    }

    .social__inner {
      display: flex;
      margin-right: auto;
      margin-left: auto;

      @media (min-width: $screen-md) {
        margin-right: 0;
        margin-left: 0;
      }

      @media (min-width: $screen-lg) {
        display: block;
      }
    }

    .social__link-wrap {
      margin-left: -1px;

      @media (min-width: $screen-lg) {
        margin-left: 0;
        margin-top: -1px;
      }

      &:first-child {
        border-top-left-radius: $border-radius;
        border-bottom-left-radius: $border-radius;

        @media (min-width: $screen-lg) {
          border-bottom-left-radius: 0;
          border-top-right-radius: $border-radius;
        }
      }

      &:last-child {
        border-top-right-radius: $border-radius;
        border-bottom-right-radius: $border-radius;

        @media (min-width: $screen-lg) {
          border-top-right-radius: 0;
          border-bottom-left-radius: $border-radius;
        }
      }
    }
  }


  // &__left > * {

  //   &:first-child {
  //     margin-top: 0;
  //   }
  // }

  // &__right {
  //   flex-grow: 1;
  //   font-size: 13px;
  //   line-height: 1.4;
  //   overflow: hidden;

  //   @media (min-width: $screen-lg) {
  //     grid-row-start: 1;
  //     grid-row-end: 2;
  //     grid-column-start: 2;
  //     grid-column-end: 3;
  //     padding-left: 24px;
  //   }
  // }

  &__cards {
    margin-top: 40px;
    // margin-left: -16px;
    // margin-right: -16px;

    @media (min-width: $screen-md) {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(290px, 1fr));
      grid-template-rows: auto auto auto;
      gap: 16px;
      margin-left: 0;
      margin-right: 0;
    }

    &:first-child {
      margin-top: 0;
    }
  }

  &__cards-large {
    margin-left: -16px;
    margin-right: -16px;

    @media (min-width: $screen-md) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: auto;
      gap: 16px;
      margin-left: 0;
      margin-right: 0;
    }

    @media (min-width: $screen-xxl) {
      grid-template-columns: 1fr 1fr 1fr;
    }

    & > * {
      margin-bottom: 16px;

      @media (min-width: $screen-lg) {
        margin-bottom: 0;
      }
    }
  }

  &__text {

    @media (min-width: $screen-lg) {
      flex-basis: 75%;
    }

    @media (min-width: $screen-columns-2) {
      columns: 2;
      column-gap: 3em;
    }
  }

  &__lead {
    margin-top: 0.5em;
    font-size: 18px;
    font-weight: 700;
    line-height: 1.2;
  }

  p {
    margin-bottom: 24px;
  }

  &__bottom {
    flex-basis: 100%;
    margin-top: 20px;

    @media (min-width: $screen-lg) {
      margin-top: 0;
    }
  }

  &__list {
    margin-top: 30px;
    margin-bottom: 45px;
    padding: 0;
    list-style: none;
    font-size: 13px;
    line-height: 16px;
    font-weight: inherit;

    &:last-child {
      margin-bottom: 0;
    }

    li {
      position: relative;
      margin-bottom: 0.5em;
      padding-left: 15px;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0.6em;
        display: block;
        width: 3px;
        height: 3px;
        border-radius: 50%;
        background-color: currentColor;
      }
    }

    ul {
      margin-top: 15px;
      margin-left: 15px;
    }

    &--bold {
      font-weight: 700;

      li {

        &::before {
          width: 5px;
          height: 5px;
          top: 0.45em;
        }
      }
    }
  }

  & .page-main--article {

    &__header-inner {

      @media (min-width: $screen-lg) {
        display: flex;
      }
    }

    &__next {

      @media (min-width: $screen-lg) {
        margin-top: auto;
        margin-left: auto;
      }
    }

    &__main {
      background-color: #fff;

      @media (min-width: $screen-lg) {
        display: grid;
        grid-template-columns: auto 326px;
        grid-template-rows: auto auto;
      }
    }
  }

  .page-main--about & {

    &__header {

      @media (min-width: $screen-lg) {
        width: 100%;
      }
    }

    &__header-inner {

      @media (min-width: $screen-lg) {
        display: flex;
        flex-wrap: wrap;
        // display: grid;
        // grid-template-rows: 1fr auto;
        // grid-template-columns: minmax(274px, 274px) auto auto;
        // justify-content: stretch;
      }
    }

    &__title {

      @media (min-width: $screen-lg) {
        // grid-row-start: 1;
        // grid-row-end: 2;
        // grid-column-start: 1;
        // grid-column-end: 4;
        flex-basis: 100%;
        align-self: flex-start;
      }
    }

    &__subject {

      @media (min-width: $screen-lg) {
        // grid-row-start: 2;
        // grid-row-end: 3;
        // grid-column-start: 2;
        // grid-column-end: 3;
        align-self: end;
        margin-left: 298px;
      }
    }

    &__slogan {

      @media (min-width: $screen-lg) {
        grid-row-start: 2;
        grid-row-end: 3;
        grid-column-start: 3;
        grid-column-end: 4;
        align-self: end;
      }
    }

   &__header-text {
      margin-top: 15px;

      @media (min-width: $screen-lg) {
        grid-row-start: 2;
        grid-row-end: 3;
        grid-column-start: 3;
        grid-column-end: 4;
        align-self: end;
        width: 600px;
        margin-top: 0;
        margin-left: 298px;
      }

      @media (min-width: $screen-xl) {
        width: 250px;
        text-align: right;
      }
    }

    &__text {
      color: #fff;
    }
  }

  .page-main--contacts & {

    &__header-inner {
      z-index: 0;
      @media (min-width: $screen-lg) {
        height: 712px;
      }
    }

    &__title {
      position: relative;
      z-index: 10;
      flex-basis: auto;

      .title {
        width: auto;
        max-width: none;
      }
    }
  }
}

