// В этом файле должны быть стили для БЭМ-блока events, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.events {

  $block-name:                &; // #{$block-name}__element

  font-family: $font-family--header;
  font-size: 13px;
  color: inherit;
  overflow-y: scroll;

  .about__right & {
    margin-top: 60px;
  }

  &__inner {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-template-rows: auto;
    gap: 30px 0;
    min-width: 660px;

    @media (min-width: $screen-md) {
      grid-template-columns: auto 340px auto auto;
    }
  }

  &__item {
    padding-right: 28px;

    &--date {
      color: rgba(255, 255, 255, 0.54);
    }

    &--note {
      color: rgba(255, 255, 255, 0.54);
    }

    &#{$block-name}__header {
      margin-bottom: 8px;
      padding-bottom: 14px;
      font-weight: 700;
      color: inherit;
      border-bottom: 2px solid #f7efef;
    }
  }
}
