// В этом файле должны быть стили для БЭМ-блока main-slider, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.main-slider {

  $block-name:                &; // #{$block-name}__element

  width: 100%;
  padding: 0;

  &__item {
    position: relative;
    height: auto;
    border-radius: $border-radius;
    overflow: hidden;
    z-index: 10;

    .title {
      font-size: 30px;
      margin-bottom: 5px;
      white-space: pre-line;

      @media (min-width: $screen-md) {
        margin-bottom: 10px;
      }
    }
  }

  &__item-inner {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-right: 16px;
    padding-left: 16px;
    padding-top: 32px;
    padding-bottom: 70px;

    @media (min-width: $screen-md) {
      padding-bottom: 16px;
    }

    @media (min-width: $screen-xxl) {
      padding-bottom: 24px;
    }
  }

  &__img {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: -1;

    picture {
      display: flex;
      height: 100%;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;

      @media (min-width: $screen-md) {
        object-position: center 0;
      }
    }
  }

  &__text {
    padding-left: 16px;

    @media (min-width: $screen-md) {
      width: 240px;
    }

    @media (min-width: $screen-xxl) {
      grid-column-start: 1;
      grid-column-end: 2;
    }
  }

  &__tagline {
    margin-top: 70px;
    margin-left: auto;
    margin-bottom: 20px;
    width: 130px;
    font-family: $font-family--header;
    font-size: 18px;
    font-weight: 700;
    letter-spacing: 0.9px;
    text-align: right;
    white-space: pre-line;

    @media (min-width: $screen-md) {
      margin-top: 40px;
    }

    @media (min-width: $screen-xxl) {
      margin-top: 120px;
    }
  }

  &__enroll {
    margin-left: auto;
    white-space: normal;
  }

  &__programm {
    margin-left: auto;
    margin-top: 18px;
  }



  .page-main--index & {

    &__wrapper {
      margin-bottom: 16px;

      @media (min-width: $screen-md) {
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 1;
        grid-row-end: 2;
        margin-bottom: 0;
      }

      @media (min-width: $screen-lg) {
        grid-row-start: 1;
        grid-row-end: 2;
        grid-column-start: 1;
        grid-column-end: 3;
      }

      @media (min-width: $screen-xl) {
        grid-row-start: 1;
        grid-row-end: 2;
        grid-column-start: 2;
        grid-column-end: 4;
      }
    }



    .title {

      @media (min-width: $screen-xxl) {
        grid-column-start: 1;
        grid-column-end: 3;
      }
    }

    &__item-inner {

      @media (min-width: $screen-xxl) {
        display: grid;
        grid-template-rows: repeat(3, auto);
        grid-template-columns: 1fr auto auto;
      }
    }

    &__tagline {

      @media (min-width: $screen-xxl) {
        grid-column-start: 2;
        grid-column-end: 4;
        width: 100%;
      }
    }

    &__programm {

      @media (min-width: $screen-xxl) {
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 3;
        grid-row-end: 4;
        margin-top: 0;
        align-self: center;
      }
    }

    &__enroll {

      @media (min-width: $screen-xxl) {
        grid-column-start: 3;
        grid-column-end: 4;
        margin-top: 0;
      }
    }
  }
}

