// В этом файле должны быть стили для БЭМ-блока social, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

/*! Соцсети */
.social {

  $block-name:                &; // #{$block-name}__

  margin: 0;
  padding: 0;
  list-style: none;

  &__inner {
    display: flex;
    margin-left: auto;
    margin-right: auto;
  }


  &__link {
    display: block;
    position: relative;
    min-width: 44px;
    height: 24px;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
    }

    span {
      @include hide-text;
    }

    &:hover,
    &:focus {
      outline: none;
    }
  }

  &__link-wrap--fb #{$block-name}__link {

    &::before {
      @include sprite($fb);
    }

    &:hover,
    &:focus {

      &::before {
        @include sprite($fb-red);
      }
    }
  }

  &__link-wrap--fb-black #{$block-name}__link {

    &::before {
      @include sprite($fb-black);
    }

    &:hover,
    &:focus {

      &::before {
        @include sprite($fb-red);
      }
    }
  }

  &__link-wrap--vk #{$block-name}__link {

    &::before {
      @include sprite($vk);
    }

    &:hover,
    &:focus {

      &::before {
        @include sprite($vk-red);
      }
    }
  }

  &__link-wrap--vk-black #{$block-name}__link {

    &::before {
      @include sprite($vk-black);
    }

    &:hover,
    &:focus {

      &::before {
        @include sprite($vk-red);
      }
    }
  }

  &__link-wrap--youtube #{$block-name}__link {

    &::before {
      @include sprite($youtube);
    }

    &:hover,
    &:focus {

      &::before {
        @include sprite($yt-red);
      }
    }
  }

  &__link-wrap--youtube-black #{$block-name}__link {

    &::before {
      @include sprite($yt-black);
    }

    &:hover,
    &:focus {

      &::before {
        @include sprite($yt-red);
      }
    }
  }

  &__link-wrap--insta #{$block-name}__link {

    &::before {
      @include sprite($insta);
    }

    &:hover,
    &:focus {

      &::before {
        @include sprite($insta-red);
      }
    }
  }

  &__link-wrap--insta-black #{$block-name}__link {

    &::before {
      @include sprite($insta-black);
    }

    &:hover,
    &:focus {

      &::before {
        @include sprite($insta-red);
      }
    }
  }

  &--footer {
    margin-bottom: 15px;
    grid-column-start: 1;
    grid-column-end: 3;

    #{$block-name}__link-wrap--vk #{$block-name}__link {
      width: 55px;
    }

    #{$block-name}__link-wrap--youtube #{$block-name}__link {
      width: 80px;
    }

    @media (min-width: $screen-md) {
      grid-column-start: 2;
      grid-column-end: 3;
      grid-row-start: 3;
      align-self: end;
      margin-bottom: 0;
    }

    @media (min-width: $screen-xl) {
      grid-row-start: 2;
    }
  }

  &--about-menu {
    margin-top: 30px;
    margin-right: auto;

    @media (min-width: $screen-lg) {
      margin-top: auto;
    }

    #{$block-name}__inner {
      align-items: flex-start;

      @media (min-width: $screen-lg) {
        flex-direction: column;
      }
    }

    #{$block-name}__link {
      min-width: 64px;
      height: 48px;

      &::before {
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }

    #{$block-name}__link-wrap {
      margin-top: -1px;
      border: solid 1px #bbbbbb;

      @media (min-width: $screen-lg) {
        border: solid 1px #dcdcdc;
      }

      &:first-child {
        border-top-left-radius: 6px;
        border-bottom-left-radius: 6px;

        @media (min-width: $screen-lg) {
          border-top-right-radius: 6px;
          border-bottom-left-radius: 0;
        }
      }

      &:last-child {
          border-bottom-right-radius: 6px;
          border-top-right-radius: 6px;

        @media (min-width: $screen-lg) {
          border-bottom-left-radius: 6px;
          border-top-right-radius: 0;
        }
      }

      &::before {
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }

  &--contacts {

    #{$block-name}__link-wrap--vk-black #{$block-name}__link {
      width: 55px;
    }

    #{$block-name}__link-wrap--youtube-black #{$block-name}__link {
      width: 80px;
    }
  }
}
