// В этом файле должны быть стили для БЭМ-блока page-footer, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority


/*! Подвал страницы*/
.page-footer {

  padding-top: 10px;
  padding-bottom: 20px;
  background-color: $section-bg;

  @media (min-width: $screen-md) {
    padding-top: 15px;
  }

  &__inner {
    @include container;

    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr repeat(6, auto);

    @media (min-width: $screen-md) {
      grid-template-columns: auto auto 1fr;
      grid-template-rows: auto 1fr auto auto;
    }

    @media (min-width: $screen-lg) {
      grid-template-columns: auto auto 1fr auto;
      grid-template-rows: auto auto;
    }

    @media (min-width: $screen-xl) {
      grid-template-columns: repeat(3, auto) 1fr auto;
    }
  }

  // &__top {
  //   display: flex;
  //   flex-wrap: wrap;
  //   justify-content: space-between;
  //   width: 100%;

  //   @media (min-width: $screen-md) {
  //     display: grid;
  //     grid-template-columns: 1fr 1fr;
  //     grid-template-rows: auto 1fr;
  //   }
  //   @media (min-width: $screen-lg) {
  //     grid-template-columns: 1fr auto;
  //     grid-column-start: 1;
  //     grid-column-end: 3;
  //     margin-bottom: 38px;
  //   }
  // }


  &__copyright {
    grid-column-start: 1;
    grid-column-end: 3;
    margin-top: 28px;
    margin-bottom: 45px;
    font-family: $font-family;
    font-size: 11px;
    letter-spacing: 0.55px;
    color: rgba(255, 255, 255, 0.32);

    @media (min-width: $screen-md) {
      grid-column-start: 3;
      grid-column-end: 4;
      grid-row-start: 2;
      grid-row-end: 3;
      justify-self: end;
      margin-top: 22px;
      margin-bottom: 0;
    }

    @media (min-width: $screen-lg) {
      grid-column-start: 4;
    }

    @media (min-width: $screen-xl) {
      grid-row-start: 2;
      margin-top: 0;
      margin-bottom: 0;
      align-self: end;
    }

    @media (min-width: $screen-xxl) {
      margin-left: auto;
      margin-right: auto;
    }
  }

  // &__contacts {
  //   margin-top: 45px;
  //   margin-bottom: 45px;

  //   @media (min-width: $screen-md) {
  //     display: flex;
  //     align-items: flex-end;
  //   }

  //   @media (min-width: $screen-lg) {
  //     grid-column-start: 1;
  //     grid-column-end: 2;
  //     grid-row-start: 2;
  //     grid-row-end: 3;
  //     margin-top: 0;
  //     margin-bottom: 0;
  //   }
  // }


  &__madeby {
    grid-row-start: 6;
    grid-column-start: 1;
    grid-column-end: 3;
    margin-left: auto;
    margin-top: 35px;
    margin-right: 3px;
    display: flex;
    align-items: flex-end;
    text-decoration: none;
    color: currentColor;

    @media (min-width: $screen-md) {
      grid-row-start: 4;
      grid-row-end: 5;
      grid-column-start: 3;
      grid-column-end: 4;
      margin-top: 42px;
      margin-right: 0;
    }

    @media (min-width: $screen-lg) {
      grid-row-start: 3;
      grid-row-end: 4;
      grid-column-start: 4;
      grid-column-end: 5;
    }

    @media (min-width: $screen-xl) {
      grid-column-start: 5;
      grid-row-start: 2;
      margin-top: 0;
    }

    span {
      margin-right: 12px;
      font-size: 11px;
      font-weight: 400;
      letter-spacing: 0.55px;
    }

    &::after {
      @include sprite($lab01-white);
      content: '';
      display: block;
    }

    &:hover {
      color: currentColor;

      &::after {
        @include sprite($lab01-red);
      }
    }
  }

  &__bottom {
    display: flex;

    @media (min-width: $screen-lg) {
      grid-column-start: 2;
      grid-column-end: 3;
      grid-row-start: 2;
      grid-row-end: 3;
    }
  }
}
