// В этом файле должны быть стили для БЭМ-блока login, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.auth {

  $block-name:                &; // #{$block-name}__element

  margin-bottom: 30px;
  font-family: $font-family--header;
  color: #ffffff;

  @media (min-width: $screen-md) {
    min-height: 740px;
  }

  @media (min-width: $screen-lg) {
    display: flex;
  }

  &__enter,
  &__register {
    flex-basis: 50%;
    display: flex;
    justify-content: center;
    padding-top: 30px;
    padding-bottom: 20px;

    @media (min-width: $screen-lg) {
      padding-top: 250px;
    }

    @media (min-width: $screen-xl) {
    }
  }

  &__register {
    background-color: $red;
    border-radius: $border-radius;

    #{$block-name}__fields {
      background-image: url('../img/kimono-white.png');
      background-repeat: no-repeat;
      background-position: top center;
      background-size: 223px 223px;
    }
  }

  &__title {
    font-size: 18px;
    font-weight: 700;
  }

  &__fields {
    display: flex;
    flex-wrap: wrap;
  }

  &__field {
    width: 260px;

    &:nth-child(2n-1) {
      margin-right: 32px;
    }

    .field-text__name {
      display: block;
      padding-top: 10px;
      margin-bottom: 0;
      padding-bottom: 0;
    }
  }

  &__social {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 30px;

    @media (min-width: $screen-lg) {
      margin-top: 85px;
    }

    @media (min-width: $screen-xl) {
      flex-wrap: nowrap;
    }
  }

  &__social-text {
    margin-right: 40px;
    margin-top: 15px;
    margin-bottom: 15px;
    font-size: 10px;
    font-weight: 600;
    letter-spacing: 2px;
    color: #ffffff;
    text-transform: uppercase;
  }

  &__docs {
    margin-top: 30px;

    @media (min-width: $screen-xl) {
      margin-top: 18px;
      grid-column-start: 2;
      grid-row-start: 4;
      grid-row-end: span 2;
    }

    .download {
      width: 260px;
      margin-bottom: 16px;
    }
  }

  &__user-content {
    margin-top: 30px;

    .field-checkbox__name-text {
      cursor: pointer;
    }

    @media (min-width: $screen-xl) {
      grid-column-start: 1;
      grid-column-end: 2;
    }
  }
}

.enter {
  width: 252px;

  @media (min-width: $screen-md) {
    width: 320px;
  }

  @media (min-width: $screen-xl) {
    width: 552px;
  }

  &__submit.btn {
    margin-top: 10px;
    padding-left: 0;
    font-size: 14px;
    font-weight: 700;
    letter-spacing: 1.4px;
  }
}

.register {
  color: #000;
  width: 252px;

  @media (min-width: $screen-md) {
    width: 320px;
  }

  @media (min-width: $screen-xl) {
    width: 552px;
    display: grid;
    grid-template-columns: minmax(260px, 260px) minmax(260px, 260px);
    grid-template-rows: repeat(4, minmax(min-content, max-content)) 1fr;
    grid-auto-rows: minmax(min-content, max-content);
    gap: 0 32px;
    background-image: url('../img/kimono-white.png');
    background-repeat: no-repeat;
    background-position: top 32px center;
    background-size: 223px 223px;
  }

  .auth__title {
    grid-column-start: 1;
    grid-column-end: 3;
    }

  &__load-doc {
    grid-column-start: 2;
    margin-top: 20px;
  }

  &__load-lbl.btn {
    padding-left: 0;
    font-size: 11px;
    font-weight: 700;
    letter-spacing: 0.55px;
    color: #000000;
    text-transform: uppercase;

    &:hover,
    &:focus,
    &:active {
      color: #000;
    }
  }

  &__load-note {
    display: block;
    margin-top: 5px;
    font-size: 11px;
    font-weight: 400;
    text-align: left;
    color: rgba(0, 0, 0, 0.4);
    text-transform: none;
  }

  &__load-input,
  &__loaded-input {
    display: none;
  }

  &__submit.btn {
    margin-top: 40px;
    padding-left: 0;
    font-size: 14px;
    font-weight: 700;
    letter-spacing: 1.4px;
    text-align: left;
    color: #fff;

    &:hover,
    &:active,
    &:focus {
      color: #fff;
    }
  }
}
