// В этом файле должны быть стили для БЭМ-блока tabs, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.tabs {

  $block-name:                &; // #{$block-name}__element

  &__links {
    margin: 0;
    padding: 0;
    list-style: none;
    font-family: $font-family--header;

    &:after {
      content: '';
      display: none;
      flex-grow: 1;
      height: 1px;

      @media (min-width: $screen-md) {
        display: block;
      }
    }

    .no-js & {
      display: none;
    }
  }

  &__link-wrap {
    position: relative;
    margin-bottom: 2em;
    color: currentColor;

    &--active {
      color: $red;
    }
  }

  &__link {
    display: block;
    font-size: 16px;
    font-weight: 700;
    color: currentColor;
    text-decoration: none;

    &:hover,
    &:focus {
      color: currentColor;
    }
  }

  &__content-wrapper {
    display: block;
    padding: $field-padding-vertical $field-padding-horizontal;

    @media (min-width: $screen-md) {
      border-top: none;
    }
  }

  &__content-item {

    .js & {
      display: none;
    }

    & > p:first-child {
      margin-top: 0;
    }

    & > *:last-child { // stylelint-disable-line  selector-combinator-blacklist
      margin-bottom: 0;
    }

    &--active {

      .js & {
        display: block;
      }
    }
  }

  .about__left & {

    &__links {
      padding-left: 16px;
      margin-top: 5px;
    }
  }
}

.multi-tabs {

  &__content-item {
    display: none;

    &--active {
      display: block;
    }
  }


}
