// В этом файле должны быть стили для БЭМ-блока about-preview, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.preview {

  $block-name:                &; // #{$block-name}__element

  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  min-height: 276px;


  &__title {
    margin-top: 12px;
    margin-bottom: 24px;
    font-family: $font-family--header;
    font-size: 11px;
    letter-spacing: 2.2px;
    color: $red;
    text-transform: uppercase;
  }

  &__text {
    font-family: $font-family;
    font-size: 13px;
    line-height: 1.4;
    color: #000;

    p:nth-child(n+2) {
      display: none;
    }

    @media (min-width: $screen-lg) {
      p:nth-child(n+2) {
        display: block;
      }
    }

    @media (min-width: $screen-xl) {
      p:nth-child(n+2) {
        display: none;
      }
    }


  }

  &__more.btn {
    margin-top: auto;
    padding-left: 0;
  }

  .page-main--index & {
    @media (min-width: $screen-md) {
      margin-bottom: 0;
    }

    @media (min-width: $screen-lg) {
      grid-column-start: 2;
      grid-column-end: 4;
      grid-row-start: 2;
      grid-row-end: 3;
    }

    @media (min-width: $screen-xl) {
      grid-column-end: 3;
    }
  }
}
