// В этом файле должны быть стили для БЭМ-блока burger, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.burger {
  display: inline-block;
  position: relative;
  z-index: 1;
  width: 24px;
  border: none;
  padding: 0;
  line-height: 24px;
  background: transparent;
  user-select: none;
  cursor: pointer;
  color: #fff;

  &:hover,
  &:active,
  &:focus {
    > span,
    > span::before,
    > span::after {
      background-color: $red;
    }
  }

  > span { // stylelint-disable-line  selector-combinator-blacklist
    display: inline-block;
    position: relative;
    width: 100%;
    height: $toggler-part-height;
    vertical-align: top;
    font-size: 0;
    color: transparent;
    border-radius: 2.5px;
    background-color: $toggler-part-bg-color;
    transition: background $transition-time;
    z-index: -1;

    &:before,
    &:after {
      content: '';
      position: absolute;
      left: 0;
      width: 100%;
      height: $toggler-part-height;
      border-radius: 2.5px;
      background: $toggler-part-bg-color;
      transition: background $transition-time;
    }

    &:before {
      top: 8px;
      width: 70%;
    }

    &:after {
      top: 16px;
    }
  }

  &--header {
    margin-left: 30px;
    margin-right: auto;
    margin-bottom: 13px;
    flex-shrink: 0;

    @media (min-width: $screen-md) {
      margin-left: 40px;
      margin-top: 5px;
    }

    @media (min-width: $screen-xxl) {
      display: none;
    }
  }

  // &--close {

  //   > span { // stylelint-disable-line  selector-combinator-blacklist
  //     // transition: background $transition-time 0s;
  //     background: transparent;

  //     &:before,
