// В этом файле должны быть стили для БЭМ-блока hystory-slider, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.hystory-slider {

  $block-name:                &; // #{$block-name}__element

  .tns-slide-active + .tns-slide-active + .tns-slide-active {

    #{$block-name}__inner {
      padding-right: 0;

      @media (min-width: $screen-xxl) {
        padding-right: 38px;
      }
    }
  }

  .tns-slide-active + .tns-slide-active + .tns-slide-active + .tns-slider-active {

    #{$block-name}__inner {
      padding-right: 0;
    }
  }

  &__wrapper {

    @media (min-width: $screen-lg) {
      margin-top: 12px;
    }

    .tns-controls {

      @media (min-width: $screen-xl) {
        margin-top: 5px;
      }
    }

    .tns-controls button {
      background-color: transparent;

      &::before {
        @include sprite($arrow-right);
      }
    }
  }

  &__inner {
    box-sizing: border-box;

    @media (min-width: $screen-md) {
      padding-right: 38px;
    }
  }

  &__year {
    margin-bottom: 10px;
    font-family: $font-family--header;
    font-size: 48px;
    font-weight: 700;
    color: $red;
  }

  &__title {
    display: block;
    min-height: 2.4em;
    font-family: $font-family--header;
    font-size: 18px;
    font-weight: 700;
    color: #fff;
    text-decoration: none;

    &:hover,
    &:focus,
    &:active {
      color: $red;
      outline: none;
    }
  }

  &__text {
    min-height: 144px;
    margin-bottom: 10px;
    font-family: $font-family;
    font-size: 13px;
    line-height: 1.4;
    color: #fff;
  }
}
