// В этом файле должны быть стили для БЭМ-блока about-menu, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority


/*! Внутренне меню на страницах О клубе */
.about-nav {

  $block-name:                &; // #{$block-name}__element
  $about-menu-desktop-width: $screen-lg;

  margin-bottom: 15px;
  font-family: $font-family--header;
  background-color: $gray-light;
  border-radius: 6px;
  overflow: hidden;

  @media (min-width: $about-menu-desktop-width) {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 167px;
    left: $grid-gutter-width;
    width: 274px;
    height: 712px;
    margin-bottom: 0;
    border-radius: 6px;
    background-color: #fff;
  }

  &--open {
    #{$block-name}__inner {
      padding-bottom: 32px;
    }
  }

  &__inner {
    flex-grow: 1;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 0;
    padding-bottom: 0;

    @media (min-width: $about-menu-desktop-width) {
      padding-top: 16px;
      padding-bottom: 16px;
    }

    // мобильный вид списка
    @media (max-width: ($about-menu-desktop-width - 1)) {

      // открытие/закрытие меню только в случае работающего JS
      .js & {
        max-height: 0;
        overflow: hidden;
        transition: max-height 0.2s;
      }

      .js #{$block-name}--open & {
        max-height: 1000px;
        transition: max-height 0.6s;
      }
    }

    // настольный вид списка
    @media (min-width: $about-menu-desktop-width) {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
  }

  .inner-nav__item--active {
    display: none;

    @media (min-width: $about-menu-desktop-width) {
      display: block;
    }
  }

  &__toggle.btn {
    width: 100%;
    padding: 16px;
    text-align: left;
    font-size: 16px;
    font-weight: 700;
    text-transform: none;
    color: $red;

    @media (min-width: $about-menu-desktop-width) {
      display: none;
    }
  }

  &--contacts {
    height: auto;
    padding-bottom: 16px;

    @media (min-width: $screen-lg) {
      height: 528px;
      z-index: 20;
    }

    .about-nav__inner {
      flex-direction: row;
      justify-content: space-between;

      @media (min-width: $screen-md) {
        display: flex;
      }

      @media (min-width: $screen-lg) {
        flex-direction: column;
      }
    }

    .js & .about-nav__inner {
      padding-top: 16px;
      max-height: none;
    }

    .inner-nav__list {
    }

    .inner-nav__link {

      @media (max-width: $screen-lg - 1) {
        padding-top: 5px;
        padding-bottom: 6px;
      }
    }

    .social {
      @media (max-width: $screen-lg - 1 ) {
        margin: 0;
        margin-top: 20px;
      }
    }

  }

  &--contacts-submenu {
    position: relative;
    height: 680px;
    left: auto;
    margin-top: 30px;
    overflow: visible;

    @media (min-width: $screen-lg) {
      position: absolute;
      top: $grid-gutter-width;;
      right: $grid-gutter-width;
      margin-top: 0;
    }

    #{$block-name}__inner {
      height: 445px;
      max-height: 445px;
      padding-top: 0;
      padding-left: 0;
      padding-right: 0;
      background-color: #dcdcdc;
      border-radius: $border-radius;
      overflow-y: scroll;
    }

    .inner-nav__list {


      @media (min-width: $screen-lg) {
        width: 100%;
      }
    }

    .inner-nav__item {
      padding-top: 6px;
      padding-bottom: 5px;
      padding-left: $grid-gutter-width;
      padding-right: $grid-gutter-width;
      border-bottom: 1px solid #bababa;

      &:last-child {
        padding-bottom: $grid-gutter-width;
        border-bottom: none;
      }
    }

    .inner-nav__link {
      font-size: 11px;
      font-weight: 700;
      text-transform: uppercase;
    }

    #{$block-name}__bottom {
      padding: $grid-gutter-width;
      color: #1c1c1c;
    }
  }
}
