// В этом файле должны быть стили для БЭМ-блока news-widget, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

/*! Виджет новостей */
.news-widget {

  $block-name:                &; // #{$block-name}__element

  font-family: $font-family--header;

  @media (min-width: $screen-md) {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__title-add {
    margin-bottom: 10px;
    font-size: 11px;
    font-weight: 400;
    letter-spacing: 2.2px;
    color: rgba(255, 255, 255, 0.32);
    text-transform: uppercase;
  }

  &__title {
    margin-bottom: 26px;
    font-size: 18px;
    font-weight: 900;
    text-transform: uppercase;
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    position: relative;
    margin-bottom: 24px;

    @media (min-width: $screen-lg) {

      &:nth-child(n+4) {
        display: none;
      }
    }

    @media (min-width: $screen-xl) {

      &:nth-child(n+4) {
        display: block;
      }
    }

    &--wimg {
      height: 166px;
      margin-left: -$grid-gutter-width;
      margin-right: -$grid-gutter-width;
      padding-top: 19px;
      padding-left: 8px;
      padding-right: 8px;
      z-index: 10;

      .news-item__inner {
        &:hover,
        &:focus,
        &:active {
          color: #fff;
        }
      }
    }
  }

  &__show-all {
    padding-left: 0;

    @media (min-width: $screen-md) {
      margin-top: auto;
    }

    &:hover {
      color: #000;
    }
  }

  .page-main--index & {

    @media (min-width: $screen-md) {
      grid-row-start: 2;
      grid-row-end: 4;
      grid-column-start: 1;
      grid-column-end: 2;
      margin-bottom: 0;
    }

    @media (min-width: $screen-lg) {
      grid-row-start: 2;
      grid-row-end: 4;
      grid-column-start: 1;
      grid-column-end: 2;
    }

    @media (min-width: $screen-xl) {
      grid-row-start: 1;
      grid-row-end: 3;
      grid-column-start: 1;
      grid-column-end: 2;
    }
  }

  .page-card--thumbs-line & {

    margin-left: -16px;
    margin-right: -16px;
    border-radius: 6px;
    background-color: #252525;

    @media (min-width: $screen-md) {
      margin-left: 0;
      margin-right: 0;
      background-color: #191919;
    }

    &__list {
      @media (min-width: $screen-md) {
        display: flex;
      }
    }

    &__item {
        padding: 20px 16px;

      @media (min-width: $screen-md) {
        flex-basis: 30%;
        padding: 30px 16px;
        margin-bottom: 0;
        box-sizing: border-box;

        &:nth-child(n+4) {
          display: none;
        }
      }

      @media (min-width: $screen-lg) {
      }

      @media (min-width: $screen-xl) {

        &:nth-child(n+4) {
          display: block;
        }
        flex-basis: 25%;
      }

      &--wimg {
        margin-left: 0;
        margin-right: 0;

        @media (min-width: $screen-md) {
          height: auto;
          margin-right: 40px;
          margin-left: 16px;
        }
      }
    }
  }
}

.news-item {
  display: flex;

  &__inner {
    width: 100%;
    color: inherit;
    text-decoration: none;

    &:hover,
    &:active,
    &:focus {
      color: $red;
      outline: none;
    }
  }

  &__date {
    margin-bottom: 5px;
    font-size: 9px;
    letter-spacing: 0.45px;
    color: $red;
    text-transform: uppercase;
  }

  &__title {
    font-size: 16px;
    font-weight: 700;
  }

  &__img {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    opacity: 0.75;
    z-index: -1;

    picture {
      display: flex;
      height: 100%;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
