// В этом файле должны быть стили для БЭМ-блока mail-link, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.mail-link {

  $block-name:                &; // #{$block-name}__element

  color: #fc0202;

  @media (min-width: $screen-md) {
    align-self: end;
    margin-left: 15px;
    margin-bottom: -10px;
  }

  @media (min-width: $screen-lg) {
    grid-row-start: 3;
    grid-column-start: 3;
  }

  @media (min-width: $screen-xl) {
    grid-row-start: 2;
  }

  a {
    display: block;
    padding: 10px 0;
    font-size: 11px;
    font-weight: 700;
    letter-spacing: 0.55px;
    color: inherit;
    text-decoration: none;
    text-transform: uppercase;

    &:hover,
    &:focus,
    &:active {
      color: #000;
    }
  }

  &--footer {

    @media (min-width: $screen-md) {
      grid-column-start: 3;
      grid-column-end: 4;
    }
  }
}
