// В этом файле должны быть стили для БЭМ-блока sign-in, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.sign-in {

  $block-name:                &; // #{$block-name}__element
  padding: 16px;
  font-family: $font-family;
  font-size: 12px;
  font-weight: 400;
  border: solid 8px #252525;

  @media (min-width: $screen-md) {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }

  &__info {
    margin-bottom: 25px;

    @media (min-width: $screen-md) {
      margin-bottom: 0;
      margin-right: 40px;
    }

    p:last-child {
      margin-bottom: 0;
    }
  }

  &__phone {
    display: block;
    margin-bottom: 12px;
    font-family: $font-family--header;
    font-size: 18px;
    font-weight: 700;
    letter-spacing: 0.9px;
    color: inherit;
    text-decoration: none;

    &:hover,
    &:focus,
    &:active {
      color: inherit;
    }
  }

  &__btn {
    margin-bottom: 25px;

    @media (min-width: $screen-md) {
      margin-bottom: 0;
      margin-right: 15px;
    }
  }

  &__title {
    display: flex;
    justify-content: flex-end;
    width: 140px;
    flex-shrink: 0;
    font-family: $font-family--header;
    font-size: 18px;
    font-weight: 700;
    letter-spacing: 0.9px;

    .about__section & > * {
      display: inline;
      margin-top: 0;
      margin-bottom: 0;
    }
  }
}
