// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.main-nav {

  $block-name:                &; // #{$block-name}__element

  position: relative;
  display: flex;
  z-index: 10;

  // первый уровень
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    font-family: $font-family--header;
    font-size: 13px;
    font-weight: 700;
    letter-spacing: 2.6px;

    // мобильный вид списка
    // @media (max-width: ($menu-desktop-width - 1)) {

    //   // открытие/закрытие меню только в случае работающего JS

    //   .js & {
    //     max-height: 0;
    //     overflow: hidden;
    //     transition: max-height 0.2s;

    //     &--lvl-2 {
    //       max-height: none;
    //     }
    //   }

    //   .js #{$block-name}--open & {
    //     max-height: 1000px;
    //     transition: max-height 0.6s;

    //     &--lvl-2 {
    //       max-height: none;
    //     }
    //   }
    // }

    // настольный вид списка
    // @media (min-width: $menu-desktop-width) {
    //   display: flex;
    //   flex-wrap: wrap;

    //   &--lvl-2 {
    //     display: block;
    //   }

    //   &--lvl-3 {
    //     display: block;
    //   }
    // }
  }

  &__item {
    position: relative;

    &--active {

      & > #{$block-name}__link { // stylelint-disable-line  selector-combinator-blacklist
        color: $red;
      }
    }
  }

  &__link {
    display: block;
    padding: 0.8em 1em;
    text-decoration: none;
    text-transform: uppercase;
    color: inherit;

    &:hover,
    &:focus {
      text-decoration: none;
      color: $red;
    }

    // &--lvl-2 {
    //   min-width: 200px;

    //   @media (max-width: ($menu-desktop-width - 1)) {
    //     padding-left: 2em;
    //   }
    // }

    // &--lvl-3 {
    //   min-width: 200px;

    //   @media (max-width: ($menu-desktop-width - 1)) {
    //     padding-left: 3em;
    //   }
    // }
  }

  &--aside {
    color: #fff;

    #{$block-name}__list {
      margin-top: 55px;
    }

    #{$block-name}__link {
      padding-left: 0;
    }
  }

  &--top {
    display: none;

    @media (min-width: $screen-xxl) {
      display: block;
      margin-left: 30px;
    }

    #{$block-name}__list {
      display: flex;
    }

    #{$block-name}__item {

      @media (min-width: $screen-xxl) {
        margin-right: 10px;
      }
    }
  }

  &--footer {
    align-items: flex-start;

    #{$block-name}__list {
      font-size: 11px;
    }

    #{$block-name}__item {

      @media (min-width: $screen-lg) {
        margin-right: 18px;
      }
    }

    #{$block-name}__link {
      margin-bottom: 9px;
      padding-left: 0;
    }



    @media (min-width: $screen-md) {
      grid-column-start: 1;
      grid-column-end: 2;
      grid-row-start: 1;
      grid-row-end: 3;
      margin-bottom: 25px;
    }

    @media (min-width: $screen-lg) {
      grid-column-start: 1;
      grid-column-end: 4;
      #{$block-name}__list {
        display: flex;
        flex-wrap: wrap;
      }
    }
  }

