// В этом файле должны быть стили для БЭМ-блока media-nav, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.media-nav {

  $block-name:                &; // #{$block-name}__element

  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 4;
  border-radius: $border-radius;
  color: #000;
  background-color: #fff;

  .tabs__link-wrap {
    margin-bottom: 0;
  }

  &__inner {
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 32px;
    padding-bottom: 16px;
  }
}
