// В этом файле должны быть стили для БЭМ-блока belts, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority



/*! Пояса */

@mixin belts-size($size: sm, $cols: 10, $start: 0) {

  @for $i from 1 through $cols {
    &--ku-#{$i} {
      grid-column-start: ($cols - ($i + $start) + 1);
      @if ($size == sm) {
        grid-row-start: ($i + 2);
      }
      @else if($size == xl) {
        grid-row-start: ($i + 1);
      }
    }
  }
}

@mixin belts-color($bg, $colorfull: true) {

  @for $i from 1 through 10 {
    &--ku-#{$i} {
      @if ($colorfull) {
        background-color: map-get($bg, $i);
      }
      @else {
        &::before {
          background-color: map-get($bg, $i);
        }
      }
    }
  }
}

.belts {

  $block-name:                &; // #{$block-name}__element
  $bg: (1: #473116, 2: #946834, 3: #cb9c65, 4: #4285f4, 5: #77a3eb, 6: #69b115, 7: #f09300, 8: #f6bf26, 9: #fff, 10: #fff);

  display: grid;
  grid-template-rows: 1fr minmax(80px, 80px);
  gap: 8px 0;

  @media (min-width: $screen-xl) {
    grid-template-rows: minmax(80px, 80px);
  }

  &__item {
    display: flex;
    align-items: center;
    grid-column-end: 11;
    height: 80px;
    font-family: $font-family;
    border-radius: $border-radius;

    &--dan-1 {
      grid-row-start: 2;
    }

    &--ku-9,
    &--ku-10 {
      color: #000;

      #{$block-name}__desc {
        opacity: 0.4;
      }
    }

    @media (min-width: $screen-xl) {

      &--dan-1 {
        grid-row-start: 1;
      }
    }
  }

  &__item-inner {
    padding-left: 15px;
    padding-right: 15px;
    border-radius: $border-radius;
  }

  &__lead {
    margin-top: 0;
    font-size: 16px;
    font-weight: 700;
  }

  &__name {
    margin-bottom: 15px;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.1;
    white-space: nowrap;
  }

  &__desc {
    opacity: 0.56;
    font-size: 11px;
    line-height: 1.6;
    text-transform: uppercase;
  }

  &__text {
    grid-row-start: 1;
    grid-column-start: 1;
    grid-column-end: 12;

    @media (min-width: $screen-md) {
      grid-row-end: 6;
      grid-column-end: 10;
    }

    @media (min-width: $screen-xl) {
      grid-row-end: auto;
      grid-column-end: 7;
      width: 580px;
    }
  }

  &__card {
    padding-top: 28px;
    padding-left: 15px;
    padding-right: 10px;
    padding-bottom: 28px;
    border-radius: $border-radius;
    background-color: #dcdcdc;
    color: #000;
    hyphens: none;

    @media (min-width: $screen-md) {
      width: 302px;
    }
  }

  &__more.btn {
    padding: 0;
  }

  &--white {
    grid-template-columns: repeat(9, 1fr) 140px;

    @media (min-width: $screen-md) {
      grid-template-columns: repeat(10, 1fr);
    }

    #{$block-name}__item {
      @include belts-size(sm, 10, 2);
      @include belts-color($bg, false);
      position: relative;
      align-items: stretch;
      padding-left: 16px;
      color: #000;
      z-index: 10;

      @media (min-width: $screen-md) {
      }

      @media (min-width: $screen-xl) {
        @include belts-size(xl, 10, 2);
      }

      &::before {
        content: '';
        position: absolute;
        left: 0;
        width: 32px;
        height: 100%;
        border-radius: $border-radius;
        z-index: -1;
      }

      &--dan-1 {
        grid-column-start: 8;
        grid-column-end: 11;

        &::before {
          background-color: #000;
        }
      }
    }

    #{$block-name}__item-inner {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      width: 100%;
      padding-top: 15px;
      padding-bottom: 15px;
      background-color: #fff;
    }

    #{$block-name}__name {
      margin-bottom: 0;
    }

    #{$block-name}__desc {

      @media (min-width: $screen-md) {
        order: 2;
      }
    }

    #{$block-name}__more {

      @media (min-width: $screen-md) {
        align-self: flex-end;
      }
    }

    #{$block-name}__text {

      @media (min-width: $screen-md) {
        grid-column-end: 8;
      }
    }

    .about__bg-img {
      display: none;

      @media (min-width: $screen-md) {
        display: block;
      }
    }
  }

  &--colorfull  {
    grid-template-columns: repeat(10, 1fr);

    #{$block-name}__item {
      @include belts-size(sm);
      @include belts-color($bg, true);

      @media (min-width: $screen-xl) {
        @include belts-size(xl);
      }

      &--dan-1 {
        grid-column-start: 10;
        background-color: #191919;
      }
    }
  }
}
