// В этом файле должны быть стили для БЭМ-блока msg, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.msg {

  $block-name:                &; // #{$block-name}__element
  font-family: $font-family;
  color: #000;

  &__title {
    margin-bottom: 20px;
    font-size: 18px;
    font-weight: 700;
    color: #000000;
  }

  &__result-title {
    margin-bottom: 15px;
    font-size: 24px;
    font-weight: 700;
  }

  &__form-call {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__submit {
    margin-top: 30px;
    margin-right: auto;

    &.btn {
      padding-left: 0;
    }
  }

  &__text  {
    font-family: $font-family;
    font-size: 13px;
    opacity: 0.4;
  }

  &--result {
    width: 340px;
    margin-left: auto;
    margin-right: auto;
  }

  &__size-order.btn {
    padding-left: 0;
    font-size: 14px;
    font-weight: 700;
    letter-spacing: 1.4px;
  }

  @at-root .modal--msg-red &,
  .modal--msg-black &
   {
    color: #fff;

    &__title {
      color: #fff;
    }

    .field-text--msg .field-text__input {
        color: #fff;
        border-color: #fff;

        &::placeholder {
          color: #fff;
        }
    }

    .field-text--time {

      .field-text__input-wrap::after {
        @include sprite($arrow-down-white);
      }
    }
  }

  @at-root .modal--msg-red & {

    .msg__submit.btn {
      color: #000;
    }
  }
}
