// В этом файле должны быть стили для БЭМ-блока btn, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority


@mixin btn {
  box-sizing: border-box;
  display: inline-block;
  max-width: 100%;
  margin: 0;
  border-radius: $border-radius;
  // border: 1px solid $gray-lighter;
  border: 0;
  padding: $field-padding-vertical + 1  $field-padding-horizontal $field-padding-vertical;
  vertical-align: middle;
  text-overflow: ellipsis;
  user-select: none;
  text-align: center;
  font-size: 11px;
  font-weight: 700;
  font-style: normal;
  letter-spacing: 0.55px;
  font-family: $font-family--header;
  line-height: $line-height;
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  background-color: transparent;
  background-image: none;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;

  &:hover,
  &:focus {
    text-decoration: none;
    // background-color: darken($gray-lightest, 10%);
    // border-color: darken($gray-lighter, 12%);
  }

  // &:visited {
  //   color: $text-color;
  // }

  &:active,
  &:focus {
    // @include field-focus;
  }

  &--disabled,
  &:disabled {
    opacity: $opacity;
    cursor: not-allowed;
    color: $text-color;
    background-color: $gray-lightest;
    // border-color: $gray-lighter;

    @at-root a.btn--disabled {
      pointer-events: none;
    }
  }
}

.btn {
  @include btn;

  &--arrow-long-left {
    position: relative;
    padding-left: 25px;

    &::before {
      @include sprite($arrow-long-left-red);
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }

    &:hover {

      &::before {
        @include sprite($arrow-long-left-grey);
      }
    }
  }

  &--arrow-long-right {
    @include sprite($arrow-long-right-black);
    position: relative;

    &:hover,
    &:focus,
    &:active {
      @include sprite($arrow-long-right-white);
      outline: none;
    }

    &::after {
      // content: '';
      // position: absolute;
      // right: 0;
      // top: 50%;
      // transform: translateY(-50%);
      // @include sprite($arrow-long-right-black);
    }
  }

  &--arrow-right-after {
    position: relative;
    padding-right: 26px;

    &::after {
      @include sprite($arrow-small-right-black);
      content: '';
      position: absolute;
      right: 0;
    }
  }

  &--close {
    position: relative;
    padding: 0;
    height: 32px;
    line-height: 32px;
    font-family: $font-family--header;
    font-size: 11px;
    font-weight: 700;
    letter-spacing: 0.55px;
    // transform: translateY(-50%);

    &:hover,
    &:active,
    &:focus {
      color: $red;

      &::before,
      &::after {
        background-color: $red;
      }
    }

    &::before,
    &::after {
      position: absolute;
      top: 0;
      left: 15px;
      content: ' ';
      height: 33px;
      width: 2px;
      background-color: #fff;
    }

    &:before {
      transform: rotate(45deg);
    }

    &:after {
      transform: rotate(-45deg);
    }

    span {
      display: inline-block;
      padding-left: 44px;
      line-height: 1;
      z-index: -1;
    }
  }

  &--close-sm {
    @include sprite($close);
    width: 14px;
    height: 14px;
    padding: 0;
  }

  &--rich {
    padding: 18px 20px 16px;
    font-size: 11px;
    font-weight: 700;
    letter-spacing: 0.55px;
    border-radius: 24px;
    background-color: $red;
    text-transform: uppercase;
  }

  &--plain {
    font-size: 11px;
    font-weight: 700;
    letter-spacing: 0.55px;
    color: $red;
    text-transform: uppercase;

    &:hover,
    &:focus,
    &:active {
      color: $link-red-hover;
      outline: none;
    }
  }

  &--arrow-down-red {
    position: relative;

    &::before {
      @include sprite($arrow-down-red);
      content: '';
      position: absolute;
      right: $grid-gutter-width;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  &--edit {
    display: flex;
    color: inherit;

    &::before {
      content: '\25CF';
    }

    &::after {
      content: '\25CF';
    }
  }

  &--context {
    font-size: 11px;
    font-weight: 700;
    letter-spacing: 0.55px;
    color: inherit;

    &:hover {
      color: $red;
    }
  }

  &--white {
    color: #fff;

    &:hover,
    &:focus,
    &:active {
      color: #000;
    }
  }

  &--cart {
    @include sprite($cart-black);

    &:hover,
    &:focus,
    &:active {
      @include sprite($cart-red);
    }
  }

  // &__icon {
  //   display: inline-block;
  //   vertical-align: middle;
  // }

  // &--main {
  //   color: #fff;
  //   background-color: $color-main;
  //   // border: 1px solid $color-main;

  //   &:hover,
  //   &:focus {
  //     color: #fff;
  //     background-color: darken($color-main, 10%);
  //     // border-color: darken($color-main, 12%);
  //   }

  //   &--disabled,
  //   &:disabled {
  //     color: #fff;
  //     background-color: $color-main;
  //     // border-color: $color-main;
  //   }
  // }
}
