// В этом файле должны быть стили для БЭМ-блока about, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.about {

  $block-name:                &; // #{$block-name}__element

  &__main {
    padding-bottom: 24px;

    @media (min-width: $screen-md) {
      padding-right: $grid-gutter-width;
      padding-left: $grid-gutter-width;
    }
  }

  &__text {
    padding-left: $grid-gutter-width;
    padding-right: $grid-gutter-width;

    @media (min-width: $screen-md) {
      padding-left: 0;
      padding-right: 0;
    }

    h1,
    h2,
    h3,
    h4,
    h5 {
      white-space: pre-line;
    }
  }

  &__section {
    color: #fff;
    margin-top: 0;
    margin-bottom: 48px;

    @media (min-width: $screen-lg) {
      display: grid;
      grid-template-rows: auto;
      grid-template-columns: minmax(274px, 274px) 1fr;
      margin-top: 48px;
    }

    &:first-child {

      @media (min-width: $screen-lg) {
        min-height: 353px;
        margin-top: 24px;
      }
    }

    &:last-child {
      margin-bottom: 0;
    }

    h1,
    h2 {
      margin-top: 45px;
      margin-bottom: 25px;
      white-space: pre-line;
    }

    h3 {
      margin-top: 25px;
      margin-bottom: 20px;
      white-space: pre-line;
    }
  }

  &__left > * {

    &:first-child {
      margin-top: 0;
    }

    @media (min-width: $screen-lg) {
      padding-right: $grid-gutter-width;
    }
  }

  &__right {
    flex-grow: 1;
    font-size: 13px;
    line-height: 1.4;
    overflow: hidden;

    @media (min-width: $screen-lg) {
      grid-row-start: 1;
      grid-row-end: 2;
      grid-column-start: 2;
      grid-column-end: 3;
      padding-left: 24px;
    }

    h2:first-child {
      margin-top: 0;
    }
  }

  &__left-title {
    margin-top: 0;
    font-family: $font-family--header;
    font-size: 30px;
    font-weight: 700;
    letter-spacing: 0;
    color: #fff;
  }

  &__cards {
    margin-top: 40px;
    // margin-left: -16px;
    // margin-right: -16px;

    @media (min-width: $screen-md) {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(290px, 1fr));
      grid-template-rows: auto auto auto;
      gap: 16px;
      margin-left: 0;
      margin-right: 0;
    }

    &:first-child {
      margin-top: 0;
    }
  }

  &__cards-large {
    // margin-left: -16px;
    // margin-right: -16px;

    @media (min-width: $screen-md) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: auto;
      gap: 16px;
      margin-left: 0;
      margin-right: 0;
    }

    @media (min-width: $screen-xxl) {
      grid-template-columns: 1fr 1fr 1fr;
    }

    & > * {
      margin-bottom: 16px;

      @media (min-width: $screen-lg) {
        margin-bottom: 0;
      }
    }
  }

  &__more.btn {
    padding-left: 0;
  }

  &__text-card {
    margin-top: 30px;
    padding: 15px;
    border-radius: $border-radius;

    & > *:first-child {
      margin-top: 0;
    }

    @media (min-width: $screen-md) {
      margin-top: 48px;
      padding-top: 28px;
      padding-bottom: 28px;
      border: solid 1px rgba(255, 255, 255, 0.08);
    }
  }

  &__text-note {
    margin-top: 15px;
    font-family: $font-family;
    font-size: 13px;
    line-height: 1.4;
    text-align: right;
    color: rgba(255, 255, 255, 0.21);
  }

  &__bg-img {
    margin-top: 50px;
    margin-left: 32px;
    background-repeat: no-repeat;
    background-size: cover;
    opacity: 0.08;

    &--kimono {
      width: 216px;
      height: 216px;
      background-image: url('../img/kimono-white.png');
    }
  }

  &__note-left {
    margin-top: 30px;
    margin-bottom: 30px;
    font-size: 11px;
    line-height: 1.6;
    color: rgba(220, 220, 220, 0.32);
    text-transform: uppercase;

    &:first-child {
      margin-top: 30px;

      @media (min-width: $screen-lg) {
        margin-top: 365px;
      }
    }
  }

  &__products {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: auto;
    gap: 10px;

    @media (min-width: $screen-md) {
      grid-template-columns: 1fr 1fr;
      grid-template-rows: auto;
    }

    @media (min-width: $screen-lg) {
      grid-template-columns: 1fr;
    }

    @media (min-width: $screen-xl) {
      grid-template-columns: 1fr 1fr;
      gap: 16px;
    }

    @media (min-width: $screen-xxl) {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }

  .product-card {
    @media (min-width: $screen-lg) {
      justify-self: center;
    }
  }
}
