// В этом файле должны быть стили для БЭМ-блока person-modal, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.person-modal {

  $block-name:                &; // #{$block-name}__element
  font-family: $font-family--header;

  &__inner {
    display: flex;
    flex-wrap: wrap;

    @media (min-width: $screen-md) {
      flex-wrap: no-wrap;
    }
  }

  &__img {
    width: 274px;
    height: 220px;
    margin-right: 24px;
    margin-bottom: 20px;

    @media (min-width: $screen-lg) {
      height: 450px;
      margin-bottom: 0;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__col {
    flex-basis: 100%;

    @media (min-width: $screen-md) {
      flex-basis: 24%;
      margin-right: 20px;
    }
  }

  &__col-wide {
    flex-basis: 100%;

    @media (min-width: $screen-md) {
      flex-basis: 49%;
    }
  }

  &__type {
    margin-bottom: 20px;
    font-size: 11px;
    letter-spacing: 2.2px;
    color: #fc0202;
    text-transform: uppercase;

    @media (min-width: $screen-lg) {
      margin-bottom: 38px;
    }
  }

  &__name {
    margin-bottom: 12px;
    font-size: 30px;
    font-weight: 700;
    hyphens: auto;

    @media (min-width: $screen-xl) {
      font-size: 36px;
      max-width: 290px;
    }
  }

  &__position {
    margin-bottom: 20px;
    font-size: 11px;
    color: #adadad;
    text-transform: uppercase;

    @media (min-width: $screen-lg) {
      margin-bottom: 38px;
    }
  }

  & .person__data {
    margin: 0;
  }

  & .person__data-item {
    height: 50px;
    padding: 0;
  }

  & .person__title-list {
    width: auto;
  }

  & .person__title-item {
    height: 50px;
    padding: 0;
  }
}
