// В этом файле должны быть стили для БЭМ-блока partners-main, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.partners-main {

  $block-name:                &; // #{$block-name}__element
  font-family: $font-family;

  @media (min-width: $screen-md) {
    justify-self: end;
  }

  &__item {
    width: 94px;
    text-align: right;

    @media (min-width: $screen-md) {
      width: auto;
    }
  }

  &__img {
    font-size: 0;
    border: 1px solid #ffffff;
  }

  &__link {
    color: inherit;
    text-decoration: none;

    @media (min-width: $screen-md) {
      display: flex;
    }

    &:hover,
    &:focus,
    &:active {
      color: inherit;
    }
  }

  span {
    display: block;
    padding: 12px 0;
    font-size: 10px;
    font-weight: 700;
    letter-spacing: 0.5px;
    text-transform: uppercase;

    @media (min-width: $screen-md) {
      width: 80px;
      padding-right: 12px;
      order: -1;
      box-sizing: content-box;
    }
  }

  &--footer {
    justify-self: end;
    margin-top: 4px;

    @media (min-width: $screen-md) {
      grid-column-start: 2;
      grid-column-end: 4;
      grid-row-start: 1;
      grid-row-end: 2;
    }

    @media (min-width: $screen-lg) {
      grid-column-start: 3;
      grid-column-end: 5;
    }

    @media (min-width: $screen-xl) {
      grid-column-start: 5;
      margin-bottom: 25px;
    }
  }
}
