// В этом файле должны быть стили для БЭМ-блока filter-links, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.category-nav {

  $block-name:                &; // #{$block-name}__element

  &__list {
    padding: 0;
    padding-top: 22px;
    padding-left: 12px;
    margin: 0;
    list-style: none;
  }

  &__link {
    display: block;
    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }

    a {
      font-family: $font-family--header;
      font-size: 16px;
      font-weight: 700;
      color: $white;
      cursor: pointer;

      &:hover,
      &:active {
        color: $red;
        text-decoration: none;
        outline: none;
      }
    }

    &.active {
      a {
        color: $red;
      }
    }
  }

  @media(max-width: $screen-md) {
    margin-bottom: 30px;
  }
}
