// В этом файле должны быть стили для БЭМ-блока contacts, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.contacts {

  $block-name:                &; // #{$block-name}__element

  .tabs__content-item {

    @media (min-width: $screen-lg) {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      width: 100%;
    }
  }

  &__caption {
    width: 120px;
    margin-top: 25px;
    margin-bottom: 35px;
    font-size: 18px;
    font-weight: 700;
  }

  &__phone {
    font-size: 18px;
    font-weight: 700;
  }

  &__location {
    margin-bottom: 35px;
    font-size: 11px;
    color: #b1b1b1;
  }

  &__map {

    border-radius: $border-radius;
    overflow: hidden;

    @media (min-width: $screen-lg) {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      opacity: 0.6;
      border-radius: 0;
    }
  }

  .tabs__content-item {

    @media (max-width: $screen-lg - 1) {}
    border-radius: $border-radius;
    overflow: hidden;
  }

  .article__header-inner {
    @media (max-width: $screen-lg - 1) {
        width: auto;
    }
  }
}
