// В этом файле должны быть стили для БЭМ-блока partners, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.partners {

  $block-name:                &; // #{$block-name}__element
  margin-bottom: 24px;

  &__list {
    display: flex;
    flex-wrap: wrap;
    max-width: 1234px;
    margin: 0;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
    list-style: none;

    @media (min-width: $screen-md) {
      flex-wrap: wrap;
    }
  }

  &__img {

    &--hover {
      display: none;
    }
  }

  &__item {
    display: flex;
    justify-content: center;
    flex-basis: 100%;
    height: 112px;

    @media (min-width: $screen-md) {
      flex-basis: 25%;

      &:nth-child(4n) {

        #{$block-name}__item-inner {
          border-right: none;
        }
      }
    }

    @media (min-width: $screen-lg) {
      flex-basis: auto;
      flex-grow: 1;

      &:nth-child(4n) {

        #{$block-name}__item-inner {
          border-right: 1px solid rgba(255, 255, 255, 0.2);
        }
      }
    }

    &:last-child {

      #{$block-name}__item-inner {
        border-right: none;
      }
    }

    &:hover {

      #{$block-name}__img {
        display: none;
      }

      #{$block-name}__img--hover {
        display: block;
      }
    }
  }

  &__item-inner {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-basis: 184px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);

    @media (min-width: $screen-md) {
      flex-basis: 100%;
      border-right: 1px solid rgba(255, 255, 255, 0.2);
      border-bottom: 0;
    }

    &:hover,
    &:focus,
    &:active {
      outline: none;
    }
  }
}
