// В этом файле должны быть стили для БЭМ-блока address-desc, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.address {

  $block-name:                &; // #{$block-name}__element
  display: none;
  font-family: $font-family;
  background-color: #fff;
  color: #1c1c1c;

  &--open {
    display: block;
    position: absolute;
    height: 680px;
    padding: 16px;
    padding-bottom: 32px;
    top: 0;
    right: 0;
    left: auto;
    border-radius: $border-radius;

    @media (min-width: $screen-lg) {
      display: flex;
      justify-content: space-between;
      width: 624px;
    }

  }

  &__col {
    flex-basis: 49%;
  }

  &__name {
    margin-top: 15px;
    margin-bottom: 10px;
    font-family: $font-family--header;
    font-size: 18px;
    font-weight: 700;

    @media (min-width: $screen-lg) {
      margin-top: 30px;
      margin-bottom: 20px;
    }
  }

  &__info {
    margin-bottom: 15px;
    margin-left: 16px;
    width: 200px;

    @media (min-width: $screen-lg) {
      margin-bottom: 0;
    }
  }

  &__text {
    margin-bottom: 10px;
    font-size: 13px;
  }

  &__metro {
    span {
      margin-right: 5px;
      font-weight: 800;
    }
  }

  &__phone {
    margin-top: 18px;
    font-size: 18px;
    font-weight: 700;
  }

  &__cards {
    display: none;

    @media (min-width: $screen-lg) {
      display: block;
      margin-top: 45px;
    }
  }

  &__show-all.btn {
    margin-top: 15px;
    padding-left: 0;
  }

  &__title {
    margin-top: 15px;
    font-size: 18px;
    font-weight: 700;
    color: #000;

    @media (min-width: $screen-lg) {
      margin-top: 35px;
      margin-left: 20px;
    }

  }

  &__sign-in.btn {
    margin-top: 10px;

    @media (min-width: $screen-lg) {
      margin-top: 20px;
      margin-left: 20px;
    }
  }

  &__close.btn {
    @include sprite($close);
    position: absolute;
    top: 32px;
    right: 32px;
    width: 14px;
    height: 14px;
    padding: 0;
  }

}
