// В этом файле должны быть стили для БЭМ-блока offer-card, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.offer-card {

  $block-name:                &; // #{$block-name}__element

  background-color: $red;
  display: flex;
  flex-direction: column;
  min-height: 276px;

  &__inner {
    margin-top: 20px;
  }

  &__title {
    margin-bottom: 10px;
    font-family: $font-family--header;
    font-size: 24px;
    font-weight: 900;
    line-height: 1;
    text-transform: uppercase;
  }

  &__text {
    width: 145px;
    font-family: $font-family;
    font-size: 12px;
  }

  &__go {
    margin-left: auto;
    align-self: flex-end;
  }

  &--attest {
    display: flex;
    flex-direction: row;
    align-items: center;
    background-image: url('../img/kimono-white.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 128px 128px;
  }

  .page-main--index & {
    @media (min-width: $screen-md) {
      margin-bottom: 0;
    }
  }
}
