// В этом файле должны быть стили для БЭМ-блока user-modal, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.user-modal {

  $block-name:                &; // #{$block-name}__element
  font-family: $font-family--header;
  color: #000;

  .field-text__input {
    color: #000;
  }

  &__title {
    margin-bottom: 20px;
    font-size: 11px;
    letter-spacing: 2.2px;
    color: #fc0202;
    text-transform: uppercase;
  }

  &__name {
    display: flex;
    margin-bottom: 30px;

    .field-text {
      // flex-basis: 288px;
      margin-right: 16px;
      font-size: 36px;
      font-weight: 700;
    }

    input {
      padding: 0;
    }
  }

  &__main {
    display: flex;
  }

  &__col {
    flex-basis: 288px;
    margin-right: 16px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__img {
    position: relative;
    width: 280px;
    min-height: 276px;

    img {
      max-width: 100%;
      height: auto;
    }

    .btn--edit-img {
      position: absolute;
      top: 5px;
      right: 5px;
    }
  }

  .btn--edit-modal {
    align-self: center;
  }

  &__templates {
    display: none;
  }

  &__data {

    .field-text {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      width: 100%;

      @at-root .user-form--editing & {
        display: block;
      }
    }

    .field-text__name {
      flex-basis: 50%;
      font-size: 11px;
      color: #adadad;
      text-transform: uppercase;
    }

    .field-text__input {
      flex-basis: 50%;
      padding-right: 0;
      font-size: 12px;
      font-weight: 700;
      text-align: right;
      text-transform: uppercase;

      @at-root .user-form--editing & {
        text-align: left;
        padding-top: 0;
        padding-left: 0;
      }
    }
  }

  &__data-item {
    display: flex;
    align-items: center;
    min-height: 50px;
    border-bottom: 1px solid #dcdcdc;

    @at-root .user-form--editing & {
      align-items: flex-end;
    }
  }
}
