// Базовые цвета https://colorscheme.ru/color-converter.html

$black:                       #040404;
$gray-darkest:                #070707;
$gray-darker:                 #191919;
$gray-darken:                 #252525;
$gray-dark:                   #fc0202;
$gray:                        #bbbbbb;
$gray-light:                  #dcdcdc;
$gray-lighten:                hsl(0, 0%, 70%);
$gray-lighter:                hsl(0, 0%, 80%);
$gray-lightest:               hsl(0, 0%, 90%);
$white:                       #fff;

$red:                         #fc0202;

$color-main:                  hsl(208, 98%, 43%);
$color-success:               hsl(120, 39%, 54%);
$color-danger:                hsl(2, 64%, 58%);
$color-warning:               hsl(35, 84%, 62%);

$color-vk:                    #4a76a8;
$color-fb:                    #4267b2;
$color-tw:                    #40a2f5;
$color-gh:                    #24292e;

$link-red-hover:              #595959;



// Семантические цвета

$text-color-dark:             $black;
$text-color-light:            $white;
$text-color:                  $text-color-light;
$text-color-muted:            $gray;
$body-bg:                     $gray-darker;
$section-bg:                  $gray-darken;
$section-bg--light:           $gray-light;

$link-color:                  $color-main;
$link-color--hover:           #595959;
$border-color:                $gray-lighten;



// Базовая типографика

$font-size:                   12px;

$font-size-h1:                36px;
$font-size-h2:                24px;
$font-size-h3:                18px;
$font-size-h4:                14px;
$font-size-h5:                14px;
$font-size-h6:                14px;

$font-size-sm:                0.75em; // 12px от 16px

$line-height:                 1.2;

$typo-margin-vertical:        1em;

$font-family:                'OpenSans', -apple-system, BlinkMacSystemFont, 'Ubuntu', 'Droid Sans', 'Helvetica Neue', 'Arial', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
$font-family--header:        'GothamPro', -apple-system, BlinkMacSystemFont, 'Ubuntu', 'Droid Sans', 'Helvetica Neue', 'Arial', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
$font-family--monospace:     SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, Courier New, monospace;
// Georgia, Times New Roman, Times, serif



// Ширины

$screen-xs:                   0;
$screen-sm:                   320px;
$screen-md:                   768px;
$screen-lg:                   1024px;
$screen-xl:                   1280px;
$screen-xxl:                  1920px;

$container-sm:                ($screen-sm - 32px);
$container-md:                ($screen-md - 32px);
$container-lg:                ($screen-lg - 32px);
$container-xl:                ($screen-xl - 48px);
$container-xxl:               ($screen-xxl - 48px);

$screen-columns-2:            1680px;


// Модульная сетка

$grid-columns:                12;
$grid-gutter-width:           16px;



// Разное

$field-padding-vertical:      5px;
$field-padding-horizontal:    12px;


$border-radius:               6px;
$opacity:                     0.7;
$transition-time:             0.3s;
$shadow:                      0 4px 2px -2px rgba(0, 0, 0, 0.3);

$menu-desktop-width:          $screen-lg;
$toggler-part-height:         5px;
$toggler-part-bg-color:       #fff;

$z-index-modal:               100;
