// В этом файле должны быть стили для БЭМ-блока list-desc, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.list-desc {

  $block-name:                &; // #{$block-name}__element
  padding: 0;
  margin-top: 35px;

  &__item {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
    padding: 0;

    @media (min-width: $screen-md) {
      flex-wrap: nowrap;
    }
  }

  &__name {
    margin-bottom: 15px;
    font-family: $font-family--header;
    font-size: 16px;
    font-weight: 700;

    @media (min-width: $screen-md) {
      flex-basis: 315px;
      flex-shrink: 0;
      margin-bottom: 0;
    }
  }

  &--bullets {

    #{$block-name}__name {
      position: relative;
      padding-left: 20px;

      &::before {
        content: '';
        position: absolute;
        top: 0.5em;
        left: 0;
        width: 4px;
        height: 4px;
        border-radius: 50%;
        background-color: #fff;
      }
    }

    #{$block-name}__desc {
      min-height: 32px;
      margin-top: 5px;
      font-family: $font-family--header;
      font-size: 10px;
      line-height: 1.6;
      color: #adadad;
      text-transform: uppercase;
    }
  }
}
