// В этом файле должны быть стили для БЭМ-блока modal, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

/*! Модальное окно */
.modal {

  $block-name:                &; // #{$block-name}__element

  display: none;
  z-index: 999;

  &__backdrop {
    display: flex;
    align-items: center;
    position: fixed;
    z-index: ($z-index-modal);
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, $opacity);
    // overflow: hidden;
    -webkit-overflow-scrolling: touch;
    // overflow-y: auto;
  }

  &__dialog {
    z-index: ($z-index-modal + 1);
    overflow-x: hidden;
    overflow-y: auto;
    max-height: 100vh;

    @media (min-width: $screen-lg) {
      @include container;
      height: auto;
      margin: auto;
    }
  }

  &__content {
    position: relative;
    padding-top: 40px;
    padding-bottom: 48px;
    padding-left: 30px;
    padding-right: 30px;
    color: #000;
    background-color: #fff;
    border-radius: $border-radius;
  }

  &__close {
    @include sprite($close);
    position: absolute;
    top: 14px;
    right: 14px;
    width: 14px;
    height: 14px;
    padding: 0;
    color: rgba(0, 0, 0, 0.4);
  }

  &--open {
    display: block;
  }

  &--video {

    #{$block-name}__dialog {
      height: 90vh;
    }

    #{$block-name}__content {
      height: 100%;
    }
  }

  &--msg {

    #{$block-name}__dialog {
      width: 616px;
      padding-left: 0;
      padding-right: 0;
      box-shadow: 0px 8px 24px 0 rgba(0, 0, 0, 0.24);
    }

    #{$block-name}__content {
      padding-top: 50px;
      min-height: 222px;
    }

  }

  &--msg-red {

    #{$block-name}__content {
      background-color: $red;
    }
  }

  &--msg-black {

    #{$block-name}__content {
      background-color: #252525;
    }
  }

  &--msg-error {

    .msg__result-title {
      color: $red;
    }
  }
}
